%cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

%contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

%section_title {
	@extend %h4;

	position: relative;
	display: block;

	text-align: center;
	letter-spacing: 0.1em;

	// border-top: 1px solid $black_2;
	// border-bottom: 1px solid $black_2;

	@include bp($point_2, min) {
		padding: 3.6rem 0 3.8rem;
	}
	@include bp($point_3) {
		padding: 2rem 0 2.2rem;
	}
}

@mixin workspace_border_bottom {
	&::after {
		position: absolute;
		left: 0;
		bottom: 2.6rem;

		width: 100%;
		height: 1px;

		background-color: $black_2;

		@include bp($point_4, min) {
			content: "";
		}
	}
}

@mixin workspace_border_left {
	&::before {
		position: absolute;
		left: -3rem;
		top: 0;

		height: calc(100% - 5.8rem);
		width: 1px;

		background-color: $black_2;
		content: "";
	}
}

%text_link {
	@extend %transition_color;

	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.5;
	text-decoration: underline;
	color: $black_2;

	&:focus,
	&:hover {
		color: $black;
	}
}
