.benefits_v2 {
	padding-bottom: 4rem;
}

.benefits_v2__title {
	@extend %section_title;

	&:not(:last-child) {
		margin-bottom: 3.6rem;
	}
}

.benefits_v2__text {
	@extend %text;

	max-width: 86rem;
	margin: 0 auto;

	text-align: center;
	font-weight: 300;
	letter-spacing: 0.01em;

	&:not(:last-child) {
		margin-bottom: 5.8rem;
	}
}

.benefits_v2__wrap {
	overflow: hidden;

	&:not(:last-child) {
		margin-bottom: 5.6rem;
	}
}

.benefits_v2__list {
	@include bp($point_1, min) {
		margin: -4.8rem -4.6rem;
	}

	@include bp($point_1 - 1) {
		margin: -3rem;
	}

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch);
	}
}

.benefits_v2_item {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;

		width: 100%;
		height: 1px;

		background-color: $black_2;
	}

	&::after {
		content: "";
		position: absolute;
		right: 0;
		top: 50%;

		width: 1px;

		transform: translateY(-50%);

		background-color: $black_2;
	}

	@include bp($point_1, min) {
		padding: 4.8rem 4.6rem;
	}
	@include bp($point_1 - 1) {
		padding: 3rem;
	}

	@include bp($point_2, min) {
		width: 50%;
		flex-shrink: 0;

		&::after {
			height: calc(100% - 9.6rem);
		}
	}

	@include bp($point_3) {
		&::after {
			display: none;
		}
	}
}

.benefits_v2_item__inner {
	@include bp($point_6, min) {
		@include flex_block(row, nowrap, flex-start, stretch);
	}
}

.benefits_v2_item__content {
	@include bp($point_1, min) {
		&:not(:last-child) {
			padding-right: 4.9rem;
		}
	}

	@include bp($point_1 - 1) {
		&:not(:last-child) {
			padding-right: 2rem;
		}
	}

	@include bp($point_6, min) {
		width: 49.9%;
	}

	@include bp($point_6 - 1) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}

.benefits_v2_item__title {
	font-weight: 300;
	text-transform: uppercase;
	letter-spacing: 0.135em;
	line-height: 1.16;

	@include bp($point_2, min) {
		font-size: 2.4rem;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.8rem;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}
}

.benefits_v2_item__text {
	font-weight: 400;
	line-height: 1.5;

	@include bp($point_2, min) {
		max-width: 24.6rem;

		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.benefits_v2_item__img {
	position: relative;
	display: block;
	flex-shrink: 0;

	@include bp($point_2, min) {
		width: 50.1%;
		//padding-bottom: 41%;
		height: 25.2rem;
	}

	@media (max-width: $point_3) and (min-width: $point_6) {
		width: 50%;
		padding-bottom: 50%;
	}

	@include bp($point_6 - 1) {
		width: 100%;
		padding-bottom: 50%;
	}
}

.benefits_v2_item__img_in {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.benefits_v2_link {
	@include bp($point_2, min) {
		margin-bottom: -3rem;

		@include flex_block(row, wrap, flex-start, center);
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, center, center);
	}
}

.benefits_v2_link__in {
	@include bp($point_2, min) {
		width: 50%;
		flex-shrink: 0;
		padding: 0 2.8rem 3rem;

		&:nth-child(2n -1) {
			@include flex_block(row, nowrap, flex-end, center);
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.benefits_v2_link__btn {
	@extend %black_btn;
	@extend %btn_size_8;
}
