.text_section {
	width: 100%;

	@include bp($point_2, min) {
		padding: 9rem var(--gl-indent) 3rem;
	}

	@include bp($point_3) {
		padding: 5rem var(--gl-indent) 3rem;
	}
}

.text_section__w {
	@extend %global_flex_block_row_wrap_flex-start;
}

.text_section__title {
	font-family: $font_2;
	font-weight: 300;
	letter-spacing: -0.045rem;
	line-height: 1.11;

	color: $black_2;

	@include bp($point_2, min) {
		flex-basis: 35%;
		padding-right: 7rem;
		font-size: 4.5rem;
	}

	@include bp($point_3) {
		font-size: 2.8rem;
		margin-bottom: 1rem;
	}
}

.text_section__descrip_w {
	@include bp($point_2, min) {
		flex-basis: 65%;
	}
}

.text_section__descrip {
	font-family: $font_1;
	font-weight: 400;
	line-height: 1.5;

	color: $black_2;

	&:not(:last-child) {
		margin-bottom: 4.6rem;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.text_section__btn_w {
	margin-top: 3.2rem;

	@include bp($point_2, min) {
	}

	@include bp($point_3) {
	}
}
