.workspace {
}

.workspace__in {
	overflow: hidden;
}

.workspace__title {
	@extend %section_title;
	@extend %title_offset;
}


.workspace__list {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	.workspace--border_mod & {
		@include workspace_border_bottom;

		position: relative;

		@include bp($point_4, min) {
			margin-bottom: -2.6rem;
		}
	}

	@include bp($point_4, min) {
		margin: 0 -6rem -4.6rem 0;
	}
}

.workspace__list__item {
	position: relative;

	@include bp($point_2, min) {
		width: 33.333%;
	}
	@media (min-width: $point_4) and (max-width: $point_3) {
		width: 50%;
	}
	@include bp($point_4, min) {
		padding: 0 6rem 4.6rem 0;
	}
	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: 4rem;
	}

	&:last-child {
		margin-bottom: 0;
	}

	&:not(:nth-child(3n+4)):not(:first-child) {
		@include bp($point_2, min) {
			@include workspace_border_left;
		}
	}
	&:nth-child(2n) {
		@media (min-width: $point_4) and (max-width: $point_3) {
			@include workspace_border_left;
		}
	}
	.workspace--border_mod & {
		@include workspace_border_bottom;

		&:not(:nth-child(4n+4)):not(:first-child) {
			&::before {
				height: calc(100% - 4.6rem);
			}
		}
	}
}

