.slider__wrap {
	@extend %global_flex_block_row_nowrap_flex-start;

	width: 100%;

	@include bp($point_3) {
		flex-wrap: wrap;
	}
}

// main

.main_slider {
	position: relative;

	
	@include bp($point_2, min) {
		width: calc(100% - 23rem);
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 1.4rem;
	}

	.slick-slide {
		display: block;

		& > div {
			display: block;
		}
	}
}

.main_slider__block {
	width: 100%;
}

.main_slider__item {
	display: block !important;
	width: 100%;

	@include bp($point_2, min) {
		height: 66rem;
	}
}

.main_slider__img {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_3) {
		&:before {
			content: '';
			display: block;
			padding-bottom: 68.8%;
		}
	}
}

.main_slider__img_in {
	@extend %cover_img;

	@include bp($point_3) {
		position: absolute;
		top: 0;	
		left: 0;
	}
}

// control

.main_slider__controls {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: absolute;
	left: 0;
	bottom: 0;

	width: 100%;

	color: $white;

	z-index: 1;

	@include bp($point_2, min) {
		padding-left: 3.6rem;
	}

	@include bp($point_3) {
		padding-left: 1.8rem;
	}

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: linear-gradient(0deg, rgba($black_2, 1) 0%, rgba($black_2, 0) 100%);

		z-index: -1;
	}
}

.main_slider__arrow {
	@include bp($point_2, min) {
		width: 1.8rem;
		height: 3.6rem;
		margin-right: 2.2rem;
	}

	@include bp($point_3) {
		height: 2.4rem;
		width: 1.2rem;
		margin-right: 1.6rem;
	}

	cursor: pointer;
	transition: color .3s;

	&:hover, &:focus {
		color: $red;
	}

	&.slick-disabled {
		opacity: .4;
		pointer-events: none;
	}
}

.main_slider__count {
	font-weight: 300;
	
	@include bp($point_2, min) {
		font-size: 2.4rem;
		letter-spacing: .3rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		letter-spacing: 0.198rem;
	}
}

.main_slider__fullscreen {
	position: relative;


	margin-left: auto;
	background-color: $white;
	cursor: pointer;
	transition: background-color .3s;

	&:hover, &:focus {
		background-color: $red;

		&:before, &:after {
			background-color: $white;
		}
	}

	@include bp($point_2, min) {
		width: 8rem;
		height: 6.6rem;
	}

	@include bp($point_3) {
		width: 5rem;
		height: 4.2rem;
	}

	&:before, &:after {
		content: '';

		position: absolute;

		background-color: $red;

		@include bp($point_2, min) {
			width: 2rem;
			height: 3.2rem;
		}

		@include bp($point_3) {
			width: 1.2rem;
			height: 2rem;
		}
	}

	&:before {
		@include bp($point_2, min) {
			bottom: 1.4rem;
			left: 1.4rem;
			@include decor(.5rem, left);
		}

		@include bp($point_3) {
			bottom: .8rem;
			left: 1rem;
			@include decor(.3rem, left);
		}
	}

	&:after {
		@include bp($point_2, min) {
			top: 1.4rem;
			right: 1.4rem;
			@include decor(.5rem, right);
		}

		@include bp($point_3) {
			top: .8rem;
			right: 1rem;
			@include decor(.3rem, right);
		}
	}
}

// peview

.preview_slider {
	@include bp($point_2, min) {
		margin-left: 6rem;
		margin-bottom: -3.8rem;
	}

	@include bp($point_3) {
		width: calc(100% + 1rem);
		margin-right: -1rem;
	}

	.slick-slide {
		@include bp($point_2, min) {
			padding-bottom: 3.8rem;
		}

		@include bp($point_3) {
			padding-right: 1rem;
		}

		& > div {
			line-height: 0;
		}
	}
}

.preview_slider__item {
	
	@include bp($point_2, min) {
		width: 17rem;
		height: 10rem;
	}
}

.preview_slider__img {
	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_3) {
		position: relative;

		&:before {
			content: '';
			display: block;
			padding-bottom: 65%;
		}
	}
}

.preview_slider__img_in {
	@extend %cover_img;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
	}
}