.benefits__title {
	@extend %section_title;
	
	margin-bottom: 4rem;
}

.benefits__list {
	@extend %global_flex_block_row_wrap_flex-start_stretch;
	
	@include bp($point_2, min) {
		margin: 0 -5.2rem -3.4rem 0;
	}

	@include bp($point_3) {
		flex-direction: column;
		margin: 0 -5.2rem -4rem 0;
	}
}

.benefits__list_item {
	position: relative;

	@include bp($point_2, min) {
		width: calc(100% / 3);
		padding: 0 5.2rem 3.4rem 0;
	}

	@include bp($point_3) {
		width: 100%;
		padding: 0 5.2rem 4rem 0;
	}

	&:not(:nth-child(3n + 4)):not(:first-child) {
		&:before {
			position: absolute;
			left: -2.6rem;
			top: 0;

			height: 100%;
			width: 1px;

			background-color: $black_2;
			content: '';
		}
	}
}