.plans_card {
	height: 100%;
	@include bp($point_4, min) {
		padding-bottom: 2.6rem;
	}
}

.plans_card__picture {
	position: relative;
	display: block;
	width: 100%;
	
	@include bp($point_2, min) {
		margin-bottom: 3.6rem;
		height: clamp(10rem, 36.8vw, 50rem);
	}

	@include bp($point_3) {
		margin-bottom: 2.4rem;

		&:before {
			content: '';
			display: block;
			padding-bottom: 94%;
		}
	}
}

.plans_card__picture__floor_plan {
	position: relative;
	display: block;
	width: 100%;
	
	@include bp($point_2, min) {
		margin-bottom: 3.6rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.4rem;

		&:before {
			content: '';
			display: block;
			padding-bottom: 86%;
		}
	}
}

.plans_card__picture__in {
	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.plans_card__picture__img {
	@extend %cover_img;
}

.plans_card__picture__floor_plan__img {
	@extend %contain_img;
}

.plans_card__picture__trigger {
	position: absolute;
	right: 0;
	bottom: 0;

	background-color: $white;
	cursor: pointer;

	@include bp($point_2, min) {
		width: 6rem;
		height: 5rem;
	}

	@include bp($point_3) {
		width: 5rem;
		height: 4.2rem;
	}

	&:before {
		position: absolute;

		content: '';

		background: url("../images/icons/other_icons/shape_left_red.svg") no-repeat;
		background-position: 50% 50%;
		background-size: contain;

		@include bp($point_2, min) {
			bottom: 1.2rem;
			left: 1.2rem;

			width: 1.6rem;
			height: 2.4rem;
		}

		@include bp($point_3) {
			left: 1rem;
			bottom: .8rem;

			width: 1.2rem;
			height: 2rem;
		}
	}

	&:after {
		content: '';
		position: absolute;

		background: url("../images/icons/other_icons/shape_right_red.svg") no-repeat;
		background-position: 50% 50%;
		background-size: contain;

		@include bp($point_2, min) {
			top: 1.2rem;
			right: 1.2rem;
	
			width: 1.6rem;
			height: 2.4rem;
		}

		@include bp($point_3) {
			top: .8rem;
			right: 1rem;
	
			width: 1.2rem;
			height: 2rem;
		}

	}
}

.plans_card__title {
	text-transform: uppercase;
	letter-spacing: .13em;
	color: $black_2;
	
	@include bp($point_2, min) {
		margin-bottom: 2.4rem;

		font-size: 2.4rem;
		line-height: 2.8rem;
	}

	@include bp($point_3) {
		margin-bottom: 1.8rem;

		font-size: 1.8rem;
		line-height: 1.24;

	}
}

.plans_card__subtitle {
	@extend %text;
	font-weight: 300;
}

.plans_card__table {
	display: table;
	width: 100%;
}

.plans_card__table__row {
	display: table-row;

	& + & {
		& > * {
			border-top: 1px solid $black_2;
		}
	}
}

.plans_card__table__option {
	display: table-cell;
	
	color: $black_2;
	font-weight: 300;
	
	@include bp($point_2, min) {
		padding: 1.2rem 1.2rem 1.2rem 0;
		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		min-width: 10rem;
		padding: 1.2rem 1.2rem .8rem 0;
		font-size: 1.3rem;
		line-height: 1.4;
	}

	.plans_card__table__row:last-child & {
		padding-bottom: 0;
	}
	.plans_card__table__row:first-child & {
		padding-top: 0;
	}
}

.plans_card__table__value {
	color: $black_2;
	
	@include bp($point_2, min) {
		padding: 1.2rem 0;
		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		padding: 1.2rem 0 .8rem;
		font-size: 1.3rem;
		line-height: 1.4;
	}

	.plans_card__table__row:last-child & {
		padding-bottom: 0;
	}
	.plans_card__table__row:first-child & {
		padding-top: 0;
	}
}
