.membership_item {
	display: flex;
	flex-direction: column;

	height: 100%;
}

.membership_item__image {
	display: block;
	width: 100%;
	margin-bottom: 2.8rem;

	@include bp($point_2, min) {
		.membership__col:nth-child(4n + 1) & {
			height: 29.6rem;
		}

		&:not(.membership__col:nth-child(4n + 1) &) {
			height: 24.8rem;
		}
	}

	@media (max-width: $point_3) and (min-width: $point_4) {
		height: 29.6rem;
	}

	@include bp($point_4 - 1) {
		position: relative;
		padding-top: 60%;
	}
}

.membership_item__image_in {
	@extend %cover_img;

	@include bp($point_4 - 1) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.membership_item__content {
	flex: auto;

	&:not(:last-child) {
		margin-bottom: 3.8rem;
	}
}

.membership_item__title {
	margin-bottom: .6rem;

	font-weight: 300;
	line-height: 1.36;
	text-transform: uppercase;
	letter-spacing: 0.1em;
	color: $black_2;

	@include bp($point_2, min) {
		font-size: 2.8rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

.membership_item__descrip {
	font-weight: 400;
	line-height: 1.5;
	color: $black_2;

	p:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

.membership_item__content_head {
	margin-bottom: 2rem;
}

.membership_item__content_bottom {
}

.membership_item__fee {
	line-height: 1.55;
	text-transform: uppercase;
	letter-spacing: 1.98px;

	color: $black_2;
	@include flex_block(row, wrap, space-between, center);
}

.membership_item__fee_title {
	font-size: 1.8rem;
	font-weight: 500;
}

.membership_item__fee_price {
	font-weight: 300;
	font-size: 2rem;
}

.membership_item__fee_descrip {
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.5;

	color: $black_2;
}

.membership_item_fee__list {
	padding-top: .3rem;
}

.membership_item_fee_item {
	@include flex_block(row, wrap, space-between, center);
	color: $black_2;
}

.membership_item_fee_item__title {
	font-size: 1.6rem;
	font-weight: 300;
	line-height: 1;

	strong {
		font-size: 1.8rem;
		font-weight: 400;
		line-height: 1.45;
	}
}

.membership_item_fee_item__price {
	font-size: 1.8rem;
	font-weight: 300;
	line-height: 1.45;
	letter-spacing: 0.1em;
	text-transform: uppercase;
}

.membership_item__label_wrap {
	@include flex_block(row, wrap, flex-end, center, center);
	margin: 0 -2.2rem -1.6rem 0;
}

.membership_item__label {
	padding: 0 1.6rem 1.6rem 0;
}

.membership_item__label_in {
	position: relative;
	@include flex_block(column, wrap, center, center, center);
	width: 12.4rem;
	height: 12.4rem;
	padding: 2.8rem 1.2rem;

	border-radius: 50%;

	font-size: 1.4rem;
	font-weight: 500;
	line-height: 1;
	text-align: center;
	letter-spacing: -0.4px;

	.membership_item__label:nth-child(odd) & {
		background-color: $white;
		color: $black_2;
	}

	.membership_item__label:nth-child(even) & {
		background-color: $black_2;
		color: $white;
	}

	span {
		max-width: 10rem;
		max-height: 7.6rem;
		overflow-y: auto;

		&::-webkit-scrollbar {
			display: none;
		}

		-ms-overflow-style: none;  /* IE and Edge */
		scrollbar-width: none;  /* Firefox */
	}
}

