.events__section_title {
	@extend %section_title;

	@include bp($point_2, min) {
		margin-bottom: 6rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.4rem;
	}
}


.events_sort {
	width: 40rem;
	max-width: 100%;

	@include bp($point_2, min) {
		margin-bottom: 6rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

// event list

.event__list {
	margin-bottom: 6rem;
}

.event__item {
	@extend %global_flex_block_row_nowrap_flex-start_stretch;

	&:last-child {
		margin: 0;
	}

	@include bp($point_2, min) {
		margin-bottom: 5rem;
	}

	@include bp($point_3) {
		flex-wrap: wrap;
		margin-bottom: 3.6rem;
	}
}

.event__img {
	position: relative;
	
	display: block;
	flex-shrink: 0;

	&:before {
		content: '';
		display: block;
		padding-bottom: 62.5%;
	}

	@include bp($point_2, min) {
		width: 40rem;
		margin-right: 6rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2.8rem;
	}
}

.event__img_in {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.event__content {
	position: relative;
	// border-bottom: 1px solid $black_2;

	@include bp($point_3) {
		padding-bottom: 3.4rem;
	}
}

.event__title {
	line-height: 1.15;
	text-transform: uppercase;
	
	@include bp($point_2, min) {
		margin-bottom: .6rem;

		font-size: 2.4rem;
		letter-spacing: .3rem;
	}

	@include bp($point_3) {
		margin-bottom: 1rem;

		font-size: 1.8rem;
		letter-spacing: .18rem
	}
}

.event__info {
	@extend %global_flex_block_row_wrap_flex-start;

	
	@include bp($point_2, min) {
		margin-bottom: 1.6rem;
	}

	@include bp($point_3) {
		margin-bottom: .8rem;
	}
}

.event__info_title,
.event__info_date {
	font-weight: 300;
	line-height: 1.2;
	
	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.5rem;
	}
}

.event__info_title {
	position: relative;

	padding-right: 2.6rem;
	margin-right: .8rem;

	&:after {
		content: '';
		position: absolute;
		right: 0;
		top: 50%;

		height: 1px;
		width: 1.8rem;

		background-color: $black;
	}
}

.event__block {
	@extend %global_flex_block_row_nowrap_flex-start;

	@include bp($point_4 - 1) {
		flex-wrap: wrap;
	}
}

.event__text {
	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.4;
	}

	@include bp($point_4, min) {
		margin-right: 4rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
	}
}

.event__btn_wrap {
	flex-shrink: 0;

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.event__btn {
	@extend %btn_size_1;
	@extend %red_btn;

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.event__list_btn {
	@extend %global_flex_block_row_nowrap_center;
}