.popup_wide {
  @extend %transition_opacity;

  position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	padding: 8.5rem var(--gl-indent) 5rem;

	background-color: rgba($black_2, .8);

  z-index: 1000;

  opacity: 0;
	pointer-events: none;

  overflow-y: auto;

  &.popup--active {
		opacity: 1;
		pointer-events: initial;
	}
}

.popup_wide__in {
  @extend %global_flex_block_row_nowrap_flex-start_stretch;

  position: relative;

  margin: 0 auto;
  max-width: 132rem;
  min-height: min(72rem, 100%);
  
  background-color: $white;

  @include bp($point_2, min) {
    padding: 6rem 7rem;
  }

  @include bp($point_3, $point_4) {
    padding: 4rem 3.6rem;
  }

  @include bp($point_3) {
    padding: 4rem 1.6rem;
  }
}

.popup_wide__wrap {
  @extend %global_flex_block_row_nowrap_flex-start_stretch;

  width: 100%;
}

.popup_wide__col {
  flex-grow: 1;

  @include bp($point_2, min) {
    margin-right: 6.4rem;
  }
}

.popup_wide__content {
  position: relative;

  width: 100%;
  height: 100%;
}

.popup_wide__angle {
  position: absolute;

  display: block;
  width: 5.2rem;
  height: 8rem;

  background-color: $black;

  @include bp($point_3) {
    display: none;
  }

  &.popup_wide__angle--bottom_left {
    bottom: 0;
    left: 0;

    clip-path: polygon(0 0, 1.4rem 0, 1.4rem calc(100% - 1.4rem), 100% calc(100% - 1.4rem), 100% 100%, 0% 100%);
  }

  &.popup_wide__angle--top_right {
    top: 0;
    right: 0;

    clip-path: polygon(0 0, 100% 0, 100% 100%, calc(100% - 1.4rem) 100%, calc(100% - 1.4rem) 1.4rem, 0 1.4rem);
  }
}

.popup_wide__col_sm {
  flex-shrink: 0;
  width: 34%;

  @include bp($point_3) {
    display: none;
  }
}

.popup_wide__img_w {
  width: 100%;
  height: 100%;
}

.popup_wide__picture {
  display: block;
  width: 100%;
  height: 100%;
}

.popup_wide__img {
  @extend %cover_img;
}

.popup_wide__close {
  @extend %global_flex_block_row_nowrap_flex-start_center;

  position: absolute;
  top: -4.6rem;
  right: 0;

  cursor: pointer;
}

.popup_wide__close_text {
	@extend %transition_color;

  margin-right: 1.4rem;

  font-family: $font_1;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 0.2rem;
  color: $white;
  text-transform: uppercase;

  .popup_wide__close:hover & {
    color: $red;
  }
}

.popup_wide__close_icon {
  @extend %transition_color;

  width: 2rem;
  height: 2rem;

  color: $red;

  .popup_wide__close:hover & {
    color: $white;
  }
}
