.user__wrap {
	@include bp($point_6, min) {
		@include flex_block(row, nowrap, flex-start, flex-start);
	}
}

.user__img {
	position: relative;

	img {
		@extend %cover_img;

		position: absolute;
		top: 0;
		left: 0;
	}

	@include bp($point_4, min) {
		width: 12.8rem;
		height: 13.6rem;
	}

	@include bp($point_4, $point_6) {
		width: 50%;
		padding-bottom: 50%;
	}

	@include bp($point_6, min) {
		&:not(:last-child) {
			margin-right: 2.9rem;
		}
	}

	@include bp($point_6 - 1) {
		width: 100%;
		padding-bottom: 70%;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.user_info {
	@extend %global_flex_block_column_nowrap_flex-start;

	padding-top: 5px;
}

.user_info__name {
	font-size: 1.8rem;
	font-weight: 500;
	line-height: 1.33;
	letter-spacing: 0.025em;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}
}

.user_info__email {
	@extend %text_link;
}

.user_info__phone {
	@extend %text_link;
}
