.aspiration {
	padding-bottom: 3.2rem;

	@include bp($point_3) {
		padding-top: 4rem;
	}
}

.aspiration__title {
	@extend %h4;

	position: relative;

	text-align: center;
	letter-spacing: 3px;

	@include bp($point_2, min) {
		padding: 3.5rem 0 4.1rem;

		&:not(:last-child) {
			margin-bottom: 4.6rem;
		}
	}

	@include bp($point_3) {
		padding: 1.5rem 0 1.9rem;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.aspiration__block {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6.4rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}
}

.aspiration__row {
	@include bp($point_4, min) {
		margin: 0 -4rem -4rem 0;

		@include flex_block(row, wrap, flex-start, stretch);
	}
}

.aspiration__column {
	@include bp($point_4, min) {
		width: 50%;
		padding: 0 4rem 4rem 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.aspiration__text {
	color: $black_2;
	line-height: 1.5;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.aspiration__body {
	background-color: $black_2;

	@include bp($point_2, min) {
		padding: 4.8rem 6.4rem 5.7rem;
	}

	@media (max-width: $point_3) and (min-width: $point_4) {
		padding: 2.4rem 3.2rem 2.8rem;
	}

	@include bp($point_4 - 1) {
		padding: 1.4rem 1.2rem 1.8rem;
	}
}
