.popup_announcement__img_in {
  display: block;
  height: 100%;
}

.popup_announcement__img {
  @extend %cover_img;
}

.popup_announcement_content {
  @extend %global_flex_block_column_nowrap_center_center;

  @include bp($point_2, min) {
    padding: 2.4rem 7.4rem 0;
  }
}

.popup_announcement_info {
  margin-bottom: 2.8rem;
}

.popup_announcement__logo {
  display: block;
  width: 8.8rem;
  height: 8.8rem;

  margin-bottom: 3.6rem;
}

.popup_announcement__logo__img {
  @extend %contain_img;
}

.popup_announcement__subtitle {
	@extend %h5;

  margin-bottom: 1rem;

	letter-spacing: .13em;
  text-align: center;
}

.popup_announcement__title {
  margin-bottom: 2.8rem;

  font-family: $font_2;
	font-weight: 300;
  line-height: 5rem;
  letter-spacing: -0.1rem;
  text-align: center;
  
  @include bp($point_4, min) {
    font-size: 4.6rem;
  }
  
  @include bp($point_4 - 1) {
    font-size: 3.6rem;
  }
}

.popup_announcement__text {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
  text-align: center;

  &:not(:last-child) {
    margin-bottom: 0.8rem;
  }
}

.popup_announcement__btn {
	@extend %red_btn;
	@extend %btn_size_1;
}
