.hero_section {
	position: relative;
	width: 100%;

	@include bp($point_3) {
		min-height: var(--full-screen);
	}

	&.hero_section--full_screen_mod {
		height: var(--full-screen);
	}
}

.hero_section__in {
	max-width: calc(#{$page_width} + var(--gl-indent)*2);
	width: 100%;

	margin: 0 auto;

	&.hero_section--offset_b_mod {
		padding-bottom: 6rem;
	}

	.hero_section--v2_mod & {
		@extend %global_flex_block_column_nowrap_flex-end;
		min-height: 46rem;
		padding-bottom: 4rem;
	}

	.hero_section--v3_mod & {
		@extend %global_flex_block_column_nowrap_flex-end;
		min-height: 68rem;
		padding-bottom: 7rem;
	}

	.hero_section--center_content_mod & {
		@extend %global_flex_block_column_nowrap_center_center;
	}

	.hero_section--bottom_content_mod & {
		@extend %global_flex_block_column_nowrap_flex-start;
	}

	.hero_section--full_screen_mod & {
		height: 100%;
	}

	//-------------------Shape decore------------------------
	.hero_section--shape_decor & {

		&:before {
			position: absolute;

			display: block;
			width: 1.6rem;
			height: 2.4rem;
			background: url("../images/icons/other_icons/shape_left.svg") no-repeat;

			content: '';

			left: var(--gl-indent);

			@include bp($point_2, min) {
				bottom: 10rem;
			}

			@include bp($point_3) {
				bottom: 3.4rem;
			}
		}

		&:after {
			position: absolute;
			top: var(--height-header);

			display: block;
			width: 1.6rem;
			height: 2.4rem;
			background: url("../images/icons/other_icons/shape_right.svg") no-repeat;

			content: '';

			right: calc(var(--gl-indent) - 1px);

			@include bp($point_2, min) {}

			@include bp($point_3) {}
		}
	}
	//-------------------Shape decore###---------------------

	@include bp($point_2, min) {
		position: relative;
		min-height: 80rem;
		padding: var(--height-header) var(--gl-indent) 10rem;
	}

	@include bp($point_3) {
		min-height: var(--full-screen);
		padding: var(--height-header) var(--gl-indent) 3.4rem;
	}
	&--alt_height {
		@include bp($point_2, min) {
			min-height: 80rem;
			padding: calc(var(--height-header)*2) var(--gl-indent) 10rem;
		}
	}

	.hero_section--full_screen_mod & {
		padding: calc(2 * var(--height-header)) var(--gl-indent) calc(var(--height-header) + 10rem);
	}
}

.hero_section__bg {
	z-index: -1;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	overflow: hidden;

	.hero_section--bg_overlay_mod & {
		&::after {
			position: absolute;
			top: 0;
			left: 0;

			width: 100%;

			background: linear-gradient(180deg, $black_2 0, transparent 100%);
			content: '';

			@include bp($point_2, min) {
				height: 60%;
				opacity: .7;
			}

			@include bp($point_3) {
				height: 45%;
				opacity: .5;
			}
		}

		&::before {
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;

			background: linear-gradient(0deg, $black_2 0, transparent 100%);
			opacity: .4;
			content: '';

			@include bp($point_2, min) {
				height: 30%;
			}

			@include bp($point_3) {
				height: 45%;
			}
		}
	}
}

.hero_section__bg_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

//------------------------Breadcrumbs-----------------------
.hero_section__breadcrumbs {
	position: absolute;
	top: var(--height-header);
	left: var(--gl-indent);
	margin-top: 2.8rem;
}

.hero_section__breadcrumbs_list {
	@extend %global_flex_block_row_nowrap_flex-start;
	margin: 0 -1.8rem -.8rem 0;
}

.hero_section__breadcrumbs_item {
	position: relative;

	padding: 0 1.8rem .8rem 0;

	&:not(:last-child) {
		&:after {
			position: absolute;
			top: .46em;
			right: .6rem;

			display: block;
			width: 4px;
			height: .8rem;
			background: url("../images/icons/other_icons/breadcrumbs_arrow.svg") no-repeat;
			background-size: cover;

			content: '';
		}
	}
}

.hero_section__breadcrumbs_link {
	font-family: $font_1;

	letter-spacing: 0.05rem;
	text-transform: uppercase;

	color: $white;

	&:focus,
	&:hover {
		text-decoration: none;
	}

	&:not(.hero_section__breadcrumbs_link--weight_mod) {
		font-weight: 400;
	}

	&.hero_section__breadcrumbs_link--weight_mod {
		font-weight: 600;
	}

	@include bp($point_2, min) {
		font-size: 1.2rem;
	}

	@include bp($point_3) {
		font-size: 1rem;
	}
}

//------------------------Breadcrumbs###--------------------

.hero_section__video {
	z-index: -1;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	overflow: hidden;

	&:before, &:after {
		position: absolute;
		top: 0;
		left: 0;

		content: '';

		width: 100%;
	}

	&:before {
		z-index: 2;

		height: 100%;

		background-color: $black;

		opacity: 0.32;
	}

	&:after {
		z-index: 3;

		// height: 61.4%;
		height: 100%;

		transform: rotate(-180deg);
		background-image: linear-gradient(-3deg, #000000 0%, #000000 0%, rgba(0, 0, 0, 0.01) 100%);
		opacity: 0.4;
	}
}

.hero_section__video_in {
	z-index: 1;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

.hero_section__title {
	width: 100%;
	//max-width: 60rem;

	font-weight: 400;
	line-height: 1.08;
	text-align: center;
	text-transform: uppercase;
	color: $white;

	@include bp($point_2, min) {
		font-size: 6rem;
		letter-spacing: 0.8rem;
	}

	@include bp($point_3) {
		font-size: 3.2rem;
		letter-spacing: 0.46rem;
	}

	.hero_section--height_2_mod & {
		max-width: 80rem;

		@include bp($point_2, min) {
			margin-top: 11rem;
		}

		@include bp($point_3) {
			margin-top: 6rem;
		}
	}

	.hero_section--v3_mod & {
		max-width: 99rem;
		text-align: left;
	}
}

.hero_section__subtitle {
	margin-top: 1rem;

	font-weight: 300;
	letter-spacing: 0.3rem;
	line-height: 1.27;
	text-align: center;
	text-transform: uppercase;

	color: $white;

	@include bp($point_2, min) {
		font-size: 3rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
	}
}

//------------------------Bottom text & decor-----------------------

.hero_section__bottom {
	max-width: 100%;
}

.hero_section__bottom_text {
	font-weight: 300;
	line-height: 1.26;
	text-align: center;
	text-transform: uppercase;
	color: $white;

	@include bp($point_2, min) {
		letter-spacing: 3px;
		font-size: 3rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
		letter-spacing: 2.2px;
	}
}

.hero_section_bottom_decor {

	@include bp($point_2, min) {
		display: none;
	}
}

//------------------------Bottom text & decor###-------------------
//------------------------Picture BG-----------------------
.hero_section__bg {
	z-index: -1;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	overflow: hidden;
}

.hero_section__bg_img {
	@extend %cover_img;
}
//------------------------Picture BG###--------------------
//------------------------Logo IMG-----------------------
.hero_section__logo {
	position: absolute;
	top: 50%;
	left: 50%;

	transform: translate(-50%, -50%);
	display: block;

	@include bp($point_2, min) {
		width: 16.8rem;
		height: 16.8rem;
	}

	@include bp($point_3) {
		width: 12rem;
		height: 12rem;
	}
}

.hero_section__logo_img {
	@extend %cover_img;
}
//------------------------Logo IMG###--------------------
