//---------------------------------------------fonts
$font_path: "../fonts/";
$font_1: 'GalanoGrotesque', sans-serif;
$font_2: 'SuisseWorks', sans-serif;
$font_3: 'Helvetica', sans-serif;
$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;
$black_2: #1B2124;
$gray: #E2E3E4;
$gray_2: #C6C2B3;
$red: #F04E37;
$green: #D2E390;

$color_text_base: $black_2;
$color_link_base:#4aa3df;
$body_bg: $gray;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (
	popup : 101,
	header : 100,
	footer : 100
);
//---------------------------------------------z-index###

//---------------------------------------------layout
$page_width: 132rem;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_radius: 0;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 4rem;
$input_offset: 0 1.6rem;
$input_font_size: $font_size_base;
$input_border_color: $black_2;
$input_border_color_active: lighen($black_2, 5%);
$input_placeholder_color: rgba($black_2, .4);
$input_text_color: $black_2;
$input_radius: $gl_radius;
//---------------------------------------------forms###
