.main_menu {
	z-index: 90;

	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100vh;
	max-height: 100vh;

	background-color: $black_2;

	opacity: 0;
	transform: translateY(-101%);
	pointer-events: none;

	transition: opacity .3s ease, transform .3s ease;
	will-change: opacity, transform;

	body.body--open_menu_state & {
		opacity: 1;
		transform: translateY(0);
		pointer-events: auto;
	}

	padding-top: var(--height-header);
}

.main_menu__scroll_area {
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		padding: 0 var(--gl-indent) 17rem;
	}

	@include bp($point_3) {
		padding: 0 var(--gl-indent) 6.2rem;
	}

	overflow-x: hidden;
	overflow-y: auto;
}

.main_menu__in {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	position: relative;

	width: 100%;
	max-width: $page_width;
	margin: 0 auto;

	border-bottom: 1px solid rgba($white, .8);

	&:before {
		position: absolute;
		bottom: 0;
		left: 0;

		content: '';

		background: url("../images/icons/other_icons/shape_left.svg") no-repeat;
		background-position: 50% 50%;
		background-size: contain;

		@include bp($point_2, min) {
			width: 1.6rem;
			height: 2.4rem;
		}
		@include bp($point_3) {
			width: 1.2rem;
			height: 2rem;
		}
	}

	&:after {
		position: absolute;
		top: 0;
		right: 0;

		content: '';

		background: url("../images/icons/other_icons/shape_right.svg") no-repeat;
		background-position: 50% 50%;
		background-size: contain;

		@include bp($point_2, min) {
			width: 1.6rem;
			height: 2.4rem;
		}
		@include bp($point_3) {
			width: 1.2rem;
			height: 2rem;
		}
	}

	@include bp($point_2, min) {
		padding: 6.8rem 0 5.2rem;
	}
	@include bp($point_3) {
		padding: 4.4rem 1rem;
	}
}

.main_menu__col {
	@include bp($point_2, min) {
		width: 50%;
		min-height: calc(100vh - (var(--height-header) + 29rem + 1px));

		&:first-child {
			padding-right: 1rem;
		}

		&:last-child {
			@include flex_block(column, nowrap, flex-start, flex-end);

			padding-left: 1rem;
		}
	}

	@include bp($point_3) {
		width: 100%;

		&:first-child {
			margin-bottom: 3rem;
		}
	}
}

.menu_nav {
	width: 100%;
}

.menu_nav__list {
	width: 100%;
	margin: 0;
	padding: 0;

	list-style: none;
}

.menu_nav__list_item {
	width: 100%;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}

	&.menu_nav__list_item--has_submenu {
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}
	}
}

.menu_link {
	@extend %transition_color;

	text-decoration: none;
	color: $white;
	font-weight: 300;
	text-transform: uppercase;

	&:hover, &:focus {
		text-decoration: none;
		color: $red;
	}

	@include bp($point_2, min) {
		font-size: 2.4rem;
		letter-spacing: 0.3rem;
		line-height: 1.17;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		letter-spacing: 0.16em;
		line-height: 1.22;
	}

	.menu_nav__list_item--has_submenu & {
		@include bp($point_2, min) {
			margin-bottom: 2.4rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}
}

.menu_nav__sublist {
	padding-left: 3.6rem;
	border-left: 1px solid $white;
	margin-top: 2.4rem;
}

.menu_nav__subitem:not(:last-child) {
	@include bp($point_2, min) {
		margin-bottom: 1.4rem;
	}

	@include bp($point_3) {
		margin-bottom: .8rem;
	}
}

.menu_nav__sublink {
	@extend %transition_color;
	
	font-weight: 300;
	font-size: 1.8rem;
	font-family: $font_1;
	color: $white;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: $red;
	}
}

.menu_contact {
	@include bp($point_2, min) {
		@include flex_block(column, nowrap, flex-start, flex-end);
		margin-top: auto;
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, center, center);
	}
}

.menu_contact__social {
	@include bp($point_2, min) {
		margin-bottom: 3rem;
	}
	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}

.menu_btn_list {
	margin: 0 0 3rem;
	padding: 0;

	list-style: none;
}

.menu_btn_list__item {
	&:not(:last-child) {
		margin-bottom: 2rem;
	}

	@include bp($point_3) {
		text-align: center;
	}
}

.menu_btn {
	@extend %white_btn;
	@extend %btn_size_3;
}

.menu_button {
	@extend %white_btn;
	@extend %btn_size_5;
}

.menu_contact__list {
	margin: 0;
	padding: 0;
	list-style: none;
}


.menu_contact__logo {
	@include bp($point_2, min) {
		width: 16.2rem;
		margin: 0 0 1rem;
	}
	@include bp($point_3) {
		width: 9.8rem;
		margin: 0 0 .8rem;
	}
}

.menu_contact__logo_img {
	display: block;
	width: 100%;
	height: auto;
}

.menu_contact__list_item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_2, min) {
		text-align: right;
	}
	@include bp($point_3) {
		text-align: center;
	}
}

.contact_link {
	color: $white;
	font-weight: 400;

	&:not(.contact_link--text_mod) {
		@extend %transition_color;

		text-decoration: none;

		&:hover,
		&:focus {
			text-decoration: none;
			color: $red;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		line-height: 1.1;
	}
}
