.interactive_map {
  padding-bottom: 4rem;
}

.interactive_map__columns {
  display: flex;
  flex-wrap: wrap;

  margin: 0 -9.8rem -6rem 0;
}

.interactive_map__column {
  $map_width_on_desktop: 51%;

  padding: 0 9.8rem 6rem 0;

  @include bp($point_3) {
    width: 100%;
  }

  &.interactive_map__column--map_mod {
    @include bp($point_2, min) {
      width: $map_width_on_desktop;
      height: 88.4rem;
    }

    @include bp($point_3) {
      order: 1;
      
      height: 60rem;
    }
  }

  &.interactive_map__column--text_mod {
    @include bp($point_2, min) {
      width: 100% - $map_width_on_desktop;
    }
  }
}

.interactive_map__map {
  width: 100%;
  height: 100%;
}

.interactive_map__title {
  @extend %section_title;

  padding: 3rem 0;
  margin-bottom: 5.8rem;

  text-align: left;
}

.interactive_map__locations_item {
  display: flex;
  align-items: center;

  font-size: 2rem;
  line-height: 2.8rem;

  &:not(:last-child) {
    margin-bottom: 1.2rem;
  }
}

.interactive_map__locations_item_number {
  flex-shrink: 0;
  
  display: block;
  width: 3rem;
  margin-right: 1.6rem;

  font-weight: 500;

  transition: transform 0.3s ease;

  .interactive_map__locations_item--active_mod & {
    @include bp($point_2, min) {
      transform: scale(3);
    }

    @include bp($point_3) {
      transform: scale(2.5);
    }
  }
}

.interactive_map__locations_item_title {
  font-weight: 300;
}