.breadcrumbs {
	width: 100%;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5rem;
		}

		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.breadcrumbs__list {
	@extend %global_flex_block_row_wrap_flex-start;

	margin: 0 -2.2rem -1rem 0;
	padding: 0;

	list-style: none;
}

.breadcrumbs_list__item {
	padding: 0 2.2rem 1rem 0;

	&:not(:last-child) {
		position: relative;

		&:after {
			position: absolute;
			top: .7rem;
			right: 1rem;

			content: '';

			width: .4rem;
			height: .8rem;

			background: url("../images/icons/other_icons/breadcrumbs_arrow_black.svg") no-repeat;
			background-size: cover;
			background-position: 50% 50%;
		}
	}
}

.breadcrumbs_list__link {
	text-decoration: none;
	color: $black_2;

	font-weight: 300;
	letter-spacing: 0.043rem;
	text-transform: uppercase;
	line-height: .8;

	&:not(.breadcrumbs_list__link--current_mod) {
		@extend %transition_color;

		&:hover, &:focus {
			text-decoration: none;
			color: $red;
		}
	}

	&.breadcrumbs_list__link--current_mod {
		font-weight: 500;
	}

	@include bp($point_4, min) {
		font-size: 1.2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1rem;
	}
}
