%h1 {
	font-size: 6.6rem;
	line-height: 7rem;
}
%h2 {
	font-size: 5.6rem;
	line-height: 6rem;
	text-transform: uppercase;
	font-family: $font_2;
	font-weight: 300;
}
%h3 {
	font-size: 3.6rem;
	line-height: 4rem;
	text-transform: uppercase;
	font-family: $font_2;
	font-weight: 300;
}
%h4 {
	text-transform: uppercase;
	font-weight: 300;

	@include bp($point_2, min) {
		font-size: 3rem;
		line-height: 3.8rem;
	}
	@include bp($point_3) {
		font-size: 2rem;
		line-height: 2.2rem;
	}
}
%h5 {
	text-transform: uppercase;
	font-weight: 300;

	@include bp($point_2, min) {
		font-size: 2.4rem;
		line-height: 2.8rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
		line-height: 2.2rem;
	}
}
%h6 {
	font-size: 1.4rem;
	line-height: 1.8rem;
	text-transform: uppercase;
	font-weight: 500;
}

%text {
	font-family: $font_1;
	font-size: 2rem;
	line-height: 2.8rem;
}

%title_offset {
	@include bp($point_2, min) {
		margin-bottom: 4rem;
	}
	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}
