%form_el_checkbox {
	position: relative;

	@extend %global_flex_block_row_nowrap_flex-start_center;

	min-height: 2.6rem;
	padding-left: 4rem;

	color: $black;
	cursor: pointer;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}

	&:before,&:after {
		position: absolute;
		top: -1px;
		left: 0;
		width: 2.6rem;
		height: 2.6rem;
		cursor:pointer;
	}

	&:before {
		content: '';
		border: 1px solid $black_2;
	}

	&:after {
		@extend %icon-check;
		@extend %transition_opacity;

		width: 1.6rem;
		height: 1.6rem;

		top: .4rem;
		left: .5rem;

		text-align: center;
		line-height: 2.6rem;
		opacity: 0;
		background: $black;

		content: '';

		.form_checkbox__element:checked + & {
			opacity: 1;
		}
	}
}