.brochure {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	text-align: center;
	background: $white;
	
	@include bp($point_2, min) {
		padding: 6rem 8rem;
	}

	@include bp($point_3) {
		padding: 3rem 2.4rem 8rem;
	}
}

.brochure__col {
	@include bp($point_2, min) {
		width: 50%;
	}

	@include bp($point_3) {
		width: 100%;
	}
		
	&:first-child {
		@include bp($point_2, min) {
			padding-right: 10rem;
		}

		@include bp($point_3) {
			margin-bottom: 4.4rem;
		}
	}

	&:last-child {
		@extend %global_flex_block_row_nowrap_flex-start;
		position: relative;
	}
}

.brochure__decor {
	content: '';
	position: absolute;
	// background-color: $black_2;

	@include bp($point_2, min) {
		width: 5.2rem;
		height: 8rem;
	}

	@include bp($point_3) {
		width: 2rem;
		height: 3rem;
	}

	&.brochure__decor--v1_mod {
		@include bp($point_2, min) {
			bottom: 0;
			left: 0;
			// @include decor(1.4rem, left);
		}

		@include bp($point_3) {
			bottom: -5rem;
			left: 0;
			// @include decor(.6rem, left);
		}
	}

	&.brochure__decor--v2_mod {
		transform: rotate(-180deg);

		@include bp($point_2, min) {
			top: 0;
			right: 0;
			// @include decor(1.4rem, right);
		}

		@include bp($point_3) {
			top: 0;
			right: 0;
			// @include decor(.6rem, right);
		}
	}
}

.brochure__img {
	display: block;
	width: 100%;
	
	@include bp($point_4, min) {
		height: 60rem;
	}

	@include bp($point_4 - 1) {
		position: relative;

		&:before {
			content: '';
			display: block;
			padding-bottom: 96%;
		}
	}
}

.brochure__img_in {
	@extend %cover_img;

	@include bp($point_4 - 1) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.brochure__content {
	max-width: 38rem;
	margin: auto;
}

.brochure__logo {
	display: block;
	
	@include bp($point_2, min) {
		width: 10.6rem;
		height: 10.6rem;
		margin: 0 auto 2.8rem;
	}

	@include bp($point_3) {
		width: 7.2rem;
		height: 7.2rem;
		margin: 0 auto 1.6rem;
	}
}

.brochure__logo_img {
	@extend %contain_img;
}

.brochure__subtitle {
	font-weight: 300;
	font-style: normal;
	text-transform: uppercase;

	@include bp($point_2, min) {
		margin-bottom: 1.4rem;

		font-size: 2.4rem;
		letter-spacing: .3rem;
	}

	@include bp($point_3) {
		margin-bottom: .4rem;

		font-size: 1.8rem;
		letter-spacing: .18rem;
	}
}

.brochure__title {
	line-height: 1.1;
	font-family: $font_2;
	font-weight: 300;
	text-align: center;
	
	@include bp($point_2, min) {
		margin-bottom: 3rem;
		
		font-size: 4.5rem;
		letter-spacing: -0.045rem;
	}

	@include bp($point_3) {
		margin-bottom: 1rem;
		font-size: 3rem;
		letter-spacing: -.02rem;
	}
}

.brochure__text {
	@include bp($point_2, min) {
		margin-bottom: 3rem;

		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;

		font-size: 1.3rem;
		line-height: 1.4;
	}
}