.dicsover_more_section {
	padding-top: 3.6rem;
}

.dicsover_more_section__wrap {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	@include bp($point_3) {
		flex-direction: column;
	}
}

.dicsover_more_section__content {
	@include bp($point_2, min) {
		width: 34.8%;

		padding-top: 4rem;
		padding-right: 4rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.dicsover_more_section__title {
	@extend %h4;
	@extend %title_offset;

	letter-spacing: 3px;

	&:not(:last-child) {
		margin-bottom: 1.3rem;
	}
}

.dicsover_more_section__text {
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.5;

	&:not(:last-child) {
		margin-bottom: 3.1rem;
	}
}

.dicsover_more_section_form {
	background-color: $white;

	@include bp($point_2, min) {
		width: 65.2%;
		padding: 4.2rem 6rem 5.8rem;
	}

	@include bp($point_3) {
		padding: 2.2rem 2.8rem 4rem;
		margin-bottom: 4rem;
		order: -1;
	}
}

.dicsover_more_section_form__wrap {
	@extend %global_flex_block_row_wrap_flex-start_stretch;
	position: relative;

	padding-bottom: 2.4rem;

	@include bp($point_4, min) {
		margin: 0 -3rem -3rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 0 -1.6rem 0;
	}
}

.dicsover_more_section_form__textarea {
	width: 100%;

	&:not(:last-child) {
		margin-bottom: 3rem;
	}

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.dicsover_more_section_form__btn {
	@extend %red_btn;
	@extend %btn_size_1;

	letter-spacing: 0.04em;

	@include bp($point_3) {
		min-width: 100%;
	}
}
