.workspace_card {
	height: 100%;
	width: 100%;
	text-align: left;
	font-family: $font_1;

	@include bp($point_4) {
		border-bottom: 1px solid $black_2;
	}

	&.workspace_card--v1_mod {
		position: relative;
		border-bottom: 1px solid $black_2;
		&::after {
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;

			background-color: $body_bg;
			content: '';

			@include bp($point_2, min) {
				height: 2.8rem;
			}
			@include bp($point_3) {
				height: 1.2rem;
			}
		}
	}

	&.workspace_card--v2_mod {

		@include bp($point_2, min) {
			padding-bottom: 3.6rem;
		}
		@include bp($point_3) {
			padding-bottom: 4rem;
		}
	}

	&.workspaceCardAnim {
		overflow: hidden;

		@include bp($point_1, min) {
			// max-height: clamp(49.8rem, 47.24vw, 63.8rem);
			max-height: 63.8rem;
		}
		@media (min-width: $point_2) and (max-width: ($point_1 - 1)) {
			max-height: 53.4rem;
		}
		@media (min-width: $point_4) and (max-width: $point_3) {
			// max-height: clamp(10rem, 50vw, 63.8rem);
			max-height: 60.2rem;
		}

		&.workspace_card--v3_mod {
			@include bp($point_1, min) {
				max-height: 62rem;
			}
			@media (min-width: $point_2) and (max-width: ($point_1 - 1)) {
				max-height: 54rem;
			}
			@media (min-width: $point_4) and (max-width: $point_3) {
				max-height: 60.2rem;
			}
			@include bp($point_4, min) {
				padding-bottom: .6rem;
			}
		}
	}

	&.workspace_card--v4_mod {
		max-height: 58.8rem;
	}
}

.workspace_card__in {
	@extend %global_flex_block_column_nowrap_flex-start;
	height: 100%;
	width: 100%;
}

.workspace_card__image {
	display: block;
	width: 100%;

	.workspace_card--v2_mod & {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.6rem;
		}
	}
	@include bp($point_4, min) {
		height: 50rem;
		min-height: 50rem;
		margin-bottom: 3rem;
	}
	@media (min-width: $point_2) and (max-width: ($point_1 - 1)) {
		height: 42.4rem;
		min-height: 42.4rem;
	}
	@include bp($point_4 - 1) {
		height: clamp(30rem, 81vw, 53rem);
		margin-bottom: 2rem;
	}
}

.workspace_card__image__in {
	@extend %cover_img;
}

.workspace_card__content {
	@extend %global_flex_block_row_nowrap_flex-start;

	position: relative;
	width: 100%;
	flex: auto;

	font-weight: 400;
	color: $black_2;

	border-top: 1px solid $black_2;

	@include bp($point_2, min) {
		padding: 2.2rem 0;
	}
	@include bp($point_3) {
		padding: 1.4rem 0;
	}

	.workspace_card--v2_mod &,
	.workspace_card--v4_mod & {
		display: block;

		font-weight: 300;
		
		padding: 0;
		border: 0;
	}

	.workspace_card--v3_mod & {
		@include bp($point_2, min) {
			padding: 2.8rem 0 ;
		}
	}
}

.workspace_card__label {
	letter-spacing: .1em;
	font-weight: 300;

	@include bp($point_2, min) {
		margin-right: 2.6rem;
		font-size: 2.4rem;
		line-height: 2.8rem;
	}
	@include bp($point_3) {
		margin-right: 2rem;
		font-size: 1.8rem;
		line-height: 2.2rem;
	}
}

.workspace_card__content__in {
	@extend %global_flex_block_column_wrap_space_between_flex-start;
	height: 100%;

	.workspace_card--v4_mod & {
		align-items: center;
	}
}

.workspace_card__content__top {
	&:not(:last-child) {
		// margin-bottom: 1.4rem;
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}
		.workspace_card--v2_mod & {
			@include bp($point_2, min) {
				margin-bottom: 1.8rem;
			}
			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}

		.workspace_card--v4_mod & {
			width: 100%;
			text-align: center;
			margin-bottom: 2rem;
		}
	}
}

.workspace_card__content__dropdown {
	@extend %global_flex_block_column_wrap_space_between_flex-start;
	flex: auto;

	.workspace_card.workspaceCardAnim & {
		padding-bottom: 2.6rem;
		// padding-bottom: 4.4rem;

		@include bp($point_4, min) {
			opacity: 0;
		}
		@include bp($point_3) {
			padding-top: .6rem;
		}
		@include bp($point_4 - 1) {
			display: none;
		}
	}
}

.workspace_card__title {
	text-transform: uppercase;
	letter-spacing: .124em;

	@include bp($point_2, min) {
		font-size: 2.4rem;
		line-height: 2.8rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
		line-height: 2.2rem;
	}

	.workspace_card--v1_mod & {
		@include bp($point_2, min) {
			margin-bottom: .4rem;
		}
	}

	.workspace_card--v4_mod & {
		@include bp($point_3) {
			font-size: 1.6rem;
			line-height: 1.1;
		}
	}
}

.workspace_card__subtitle {
	@extend %text;
	font-weight: 300;

	@include bp($point_3) {
		font-size: 1.5rem;
		line-height: 2.2rem;
	}
}

.workspace_card__descr {
	font-weight: 400;

	@include bp($point_2, min) {
		line-height: 1.5;
		font-size: 1.6rem;
	}
	@include bp($point_3) {
		line-height: 1.8rem;
		font-size: 1.4rem;
	}

	&:not(:last-child) {
		margin-bottom: 1.8rem;

		.workspace_card--v2_mod & {
			margin-bottom: 2rem;
		}
	}
}

.workspace_card__button {
	@extend %red_btn;
	@extend %btn_size_1;

	.workspace_card--v2_mod & {
		@extend %black_btn;
	}

	.workspace_card--v4_mod & {
		margin: 0 auto;
	}
}