.info_section {
	position: relative;

	width: 100%;

	.section__in {
		position: relative;
	}

	@include bp($point_2, min) {
		&.info_section--mod_1 {
			padding: 6rem var(--gl-indent);
		}

		&.info_section--mod_2 {
			padding-bottom: 4rem;

			.section__in {
				padding-top: 4.9rem;
				padding-bottom: 6.8rem;
			}
		}
	}

	@include bp($point_3) {
		&.info_section--mod_1 {
			padding: 3rem var(--gl-indent) 0;
		}

		&.info_section--mod_2 {
			padding-top: 3rem;
			padding-bottom: 3rem;

			.section__in {
				padding-top: 3rem;
				padding-bottom: 3rem;
			}
		}
	}
}

.info_section__w {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	@include bp($point_2, min) {
		margin: 0 -4rem -2rem;
	}
}

.info_section__item {

	@include bp($point_2, min) {
		&:not(:last-child) {
			border-right: 1px solid $black_2;
		}

		.info_section--mod_2 & {
			padding: 4.6rem 5.4rem 6.2rem;
			flex-basis: 50%;
		}

		&:not(.info_section--mod_2 &) {
			padding: 3rem 4rem 3.4rem;
			flex-basis: calc(100% / 3);
		}
	}

	@include bp($point_3) {
		padding: 3rem 0 3.4rem;
		flex-basis: 100%;

		&:not(:last-child) {
			border-bottom: 1px solid $black_2;
		}
	}
}

.info_section_item {
	text-align: center;
	cursor: pointer;

	&:hover .info_section_item__title {
		color: $red;
	}

	&:hover .btn_base {
		color: $red;
	}
}

.info_section_item__pic {
	display: block;
	margin: 0 auto;

	@include bp($point_4, min) {
		width: 10rem;
		height: 10rem;

		.info_section_item--mod_2 & {
			margin-bottom: 2.4rem;
		}

		.info_section_item--mod_1 & {
			margin-bottom: 1.8rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 9rem;
		height: 9rem;
		margin-bottom: 1.6rem;
	}
}

.info_section_item__pic_in {
	@extend %cover_img;
}

.info_section_item__title {

	font-family: $font_1;
	font-weight: 300;
	letter-spacing: 0.3rem;
	line-height: 1.17;
	text-align: center;
	text-transform: uppercase;

	color: $black_2;
	transition: color .3s ease;

	@include bp($point_4, min) {
		font-size: 2.4rem;

		.info_section_item--mod_2 & {
			margin-bottom: 1.7rem;
		}

		.info_section_item--mod_1 & {
			margin-bottom: 2.4rem;
		}
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1.4rem;
		font-size: 2rem;
	}
}

.info_section_item__descr {
	font-family: $font_1;
	font-weight: 400;
	line-height: 1.5;
	text-align: center;

	color: $black_2;

	.info_section_item--mod_2 & {
		max-width: 51rem;
		margin-left: auto;
		margin-right: auto;
	}

	@include bp($point_4, min) {
		font-size: 1.6rem;
		
		margin-bottom: 2.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1.4rem;
		font-size: 1.3rem;
	}
}

.info_section_item__btn_w {
	margin: 0 auto;
}

.info_section_item__btn_in {
	@extend %btn_size_7;
	@extend %black_btn;
}