.event_hero {
	padding-top: 12.8rem !important;
}

.event_hero__in {

}

.event_hero__image {
	display: block;
	width: 100%;
	height: clamp(10rem, 32vw, 43.2rem);
	margin-bottom: 5.2rem;
}

.event_hero__image__in {
	@extend %cover_img;
}

//---------------------------------------------head
.event_hero__head {
	@extend %global_flex_block_row_wrap_flex-start_center;

	position: relative;
	// border-top: 1px solid $black_2;
	// border-bottom: 1px solid $black_2;
	
	@include bp($point_2, min) {
		padding: 5.6rem 7.2rem 5rem 0;
		margin-bottom: 4rem;
	}

	@include bp($point_3) {
		padding: 3rem 0;
		margin-bottom: 3rem;
	}
	
	.event_hero__image + & {
		border-top: 0;
		padding-top: 0;

		.line_decor--v1_mod {
			display: none;
		}
	}
}

.event_hero__head__title_w {
	color: $black_2;

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2rem;
	}
}

.event_hero__head__title {
	margin-bottom: .8rem;
	line-height: .9;
	font-family: $font_2;
	font-weight: 300;
	
	@include bp($point_2, min) {
		font-size: 4.6rem;
	}

	@include bp($point_3) {
		font-size: 3.4rem;
	}
}

.event_hero__head__subtitle {
	@extend %h5;

	letter-spacing: .13em;
}

.event_hero__head__date {

}

.event_hero__head__socials {
	color: $black_2;
	
	@include bp($point_2, min) {
		margin-left: auto;
	}
}

.event_hero__head__socials__title {
	margin-bottom: 1.2rem;
	font-size: 1.4rem;
	font-weight: 500;
	text-transform: uppercase;
}
//---------------------------------------------head###

//---------------------------------------------content
.event_hero__content {
	max-width: 96rem;
}

.event_hero__content__title {
	@extend %text;

	margin-bottom: 1.8rem;
	font-weight: 300;

	@include bp($point_3) {
		font-size: 1.7rem;
	}
}

.event_hero__content__descr {

	&:not(:last-child) {
		margin-bottom: 4rem;
	}
}

.event_hero__content__info {
	display: inline-flex;
	width: 100%;
	margin-bottom: 1.4rem;

	&.event_hero__content__info--offset_mod {
		margin-bottom: 4rem;
	}

	@include bp($point_3) {
		flex-wrap: wrap;
	}
}

.event_hero__content__info__label {
	margin-right: .6rem;
	font-weight: 600;

	@include bp($point_3) {
		width: 100%;
	}
}

.event_hero__content__info__value {
	a {
		color: $red;
		text-decoration: underline;

		&:hover,
		&:focus {
			text-decoration: none;
		}
	}
}
//---------------------------------------------content###