.membership {}

.membership__wrap {}

.membership__row {

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, flex-start);
		margin: 0 -3rem;
	}

	@media (max-width: $point_3) and (min-width: $point_4) {
		@include flex_block(row, wrap, flex-start, stretch, flex-start);
		margin: 0 -3rem -3rem;
	}
}

.membership__col {

	@include bp($point_2, min) {
		padding: 0 3rem;

		&:nth-child(4n + 1) {
			width: 33.2%;
		}

		&:not(:nth-child(4n + 1)) {
			width: calc(66.8% / 3);
		}

		&:nth-child(4n + 2) {
			padding-top: 9.6rem;
		}

		&:nth-child(4n + 3) {
			padding-top: 16rem;
		}

		&:nth-child(4n + 4) {
			padding-top: 22.4rem;
		}

		&:not(:last-child) {
			border-right: 1px solid $black_2;
		}
	}

	@media (max-width: $point_3) and (min-width: $point_4) {
		padding: 3rem 3rem;
		border-bottom: 1px solid $black_2;
		width: 50%;

		&:not(:last-child) {
			border-right: 1px solid $black_2;
		}
	}

	@include bp($point_4 - 1) {
		padding: 3rem 0;
		border-bottom: 1px solid $black_2;
	}
}
