.news_article__in {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start);
	}
}

.news_article__content {
	flex: 1 1 auto;
	color: $black_2;
	line-height: 1.5;

	&:not(:last-child) {
		@include bp($point_2, min) {
			padding-right: 8rem;
		}
	}

	h1,
	h2,
	h3,
	h4 {
		font-size: 2rem;
		font-weight: 300;
		line-height: 1.4;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	h1 + p,
	h2 + p,
	h3 + p,
	h4 + p {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	h5 {
		font-weight: 600;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}

	p {
		&:not(:last-child) {
			margin-bottom: 5rem;
		}
	}

	ul {
		padding-left: 2.8rem;

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	p + ul {
		margin-top: -3.6rem;
	}

	li {
		padding-left: .8rem;
		list-style-type: decimal;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.news_article__details {
	min-width: 28rem;
}

.news_article__details__date {
	display: block;
	padding: 2.6rem 0;
	margin-bottom: 3rem;

	font-size: 1.4rem;
	color: $black_2;
	font-weight: 500;
	letter-spacing: .04em;
	text-transform: uppercase;

	border-top: 1px solid;
	border-bottom: 1px solid;
}

