.welcome__section_title {
	@extend %section_title;

	margin-bottom: -1px;
}

.welcome__item {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	position: relative;

	text-align: center;

	&.text_mod {
		text-align: left;
	}

	@include bp($point_2, min) {
		margin: 0 -3rem 6rem;
		padding-top: 6rem;
	}

	@include bp($point_3) {
		padding-top: 4.4rem;
		margin: 0 0 5rem;
	}

	&:last-child {
		margin-bottom: 0;
	}
}

.welcome__item_decor {
	position: absolute;
	top: 0;

	height: 1px;
	background-color: $black_2;

	@include bp($point_2, min) {
		left: 3rem;
		right: 3rem;
	}

	@include bp($point_3) {
		left: 0;
		right: 0;
	}
}

.welcome__col {
	position: relative;

	.welcome__list.v1_mod .welcome__item:nth-child(odd) & {
		@include bp($point_2, min) {
			&:first-child {
				order: 2;

				&:after {
					left: 0;
				}
			}
		}
	}

	.welcome__list.v2_mod .welcome__item:nth-child(even) & {
		@include bp($point_2, min) {
			&:first-child {
				order: 2;

				&:after {
					left: 0;
				}
			}
		}
	}

	.welcome__item.decor_mod &:after,
	.welcome__item.text_mod &:after,
	.welcome__item.bg_mod &:after {
		display: none;
	}

	@include bp($point_2, min) {
		width: 50%;
		padding: 0 3rem;

		&:first-child {
			&:after {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				height: 100%;
				width: 1px;
				background-color: $black_2;
			}
		}
	}

	@include bp($point_3) {
		width: 100%;

		&:first-child {
			margin-bottom: 4rem;
		}
	}
}

.welcome__content {
	@extend %global_flex_block_column_nowrap_center_stretch;
	height: 100%;

	.welcome__item.text_mod:nth-child(odd) & {
		@include bp($point_2, min) {
			padding-left: 4rem;
		}
	}

	.welcome__item.text_mod:nth-child(even) & {
		@include bp($point_2, min) {
			padding-right: 5rem;
		}
	}

	.welcome__item.bg_mod & {
		background-color: $white;

		@include bp($point_3) {
			padding: 5rem 4rem;
			margin-top: -1rem;
		}
	}
}

.welcome__title {
	font-weight: 300;
	text-transform: uppercase;

	@include bp($point_2, min) {
		margin-bottom: 4rem;

		font-size: 3rem;
		letter-spacing: .3rem;
	}

	@include bp($point_3) {
		margin-bottom: .8rem;

		font-size: 2rem;
		letter-spacing: 0.22rem;
	}
}

.welcome__text {
	max-width: 36rem;

	p {
		margin-bottom: 1rem;

		&:last-child {
			margin: 0;
		}
	}

	@include bp($point_2, min) {
		margin: 0 auto 3rem;

		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		margin: 0 auto 2rem;

		font-size: 1.3rem;
		line-height: 1.4;
	}

	.welcome__item.text_mod & {
		max-width: 100%
	}
}

.welcome__btn_list {
	@extend %global_flex_block_row_wrap_center;

	.welcome__item.text_mod & {
		justify-content: flex-start;
	}
}

.welcome__btn_item {
	@include bp($point_4, min) {
		margin-right: 2.4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: 1rem;
		&.welcom__btn_item-centered {
			width: auto;
		}
		&.welcom__btn_item--hero_btns {
			width: 100% !important;
			.menu_btn {
				width: 100%;
			}
		}
	}

	&:last-child {
		margin: 0;
	}

	.welcome__item--v2-mod & {
		&:first-child {
			& .btn_base {
				color: $red;
			}
		}
	}
}

.welcome__img {
	display: block;
	width: 100%;

	.welcome__item.decor_mod & {
		&:before {
			content: '';
			position: absolute;
			bottom: 0;

			background-color: $red;
		}

		@include bp($point_2, min) {
			padding-left: 13rem;

			&:before {
				left: 3rem;

				width: 20rem;
				height: 31.6rem;

				@include decor(5.2rem, left)
			}
		}

		@include bp($point_3) {
			padding-left: 6.6rem;

			&:before {
				left: 0;
				width: 10.2rem;
				height: 16rem;

				@include decor(2.6rem, left);
			}
		}
	}

	@include bp($point_2, min) {
		height: 56rem;
	}

	@include bp($point_3) {
		height: 28rem;
	}
}

.welcome__img_in {
	@extend %cover_img;
}

.welcome__decor {
	position: absolute;
	top: 100%;
	left: 3rem;

	display: none;

	&:before, &:after {
		content: '';
		position: absolute;
		bottom: 0;

		background-color: $red;
	}

	&:before {
		left: 0;

		width: 20rem;
		height: 5.2rem;
	}

	&:after {
		left: 0;

		height: 31.4rem;
		width: 5.2rem;
	}

	.welcome__item:nth-child(even) & {
		display: block;
	}
}