.product {
	padding-bottom: 0;
	@include bp($point_2, min) {
		padding-top: 13rem;
	}

	@include bp($point_3) {
		padding-top: calc(var(--height-header) + 1.6rem);
	}
}

.product__hero {
	@extend %global_flex_block_row_wrap_flex-start;

	width: 100%;
}

.product_hero__img {
	@include bp($point_4, min) {
		width: 48%;
		margin-right: 6rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: 3rem;
	}
}

.product_hero__img_in {
	display: block;
	width: 100%;

	@include bp($point_6, min) {
		height: calc(100% * 0.92);
	}

	@include bp($point_6 - 1) {
		height: 28rem;
	}

}

.product_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

.product_hero__info {

	border-top: 1px solid $black_2;

	@include bp($point_4, min) {
		width: calc(52% - 6rem);
		padding-top: 3rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		padding-top: 2.4rem;
	}
}

.product_title {
	width: 100%;

	color: $black_2;
	font-weight: 300;
	text-transform: uppercase;

	border-bottom: 1px solid $black_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}
		@include bp($point_3) {
			margin-bottom: 2.4rem;
		}
	}


	@include bp($point_2, min) {
		padding-bottom: 3.4rem;

		font-size: 3rem;
		letter-spacing: 0.3rem;
		line-height: 1.27

	}

	@include bp($point_3) {
		padding-bottom: 1.8rem;

		font-size: 2rem;
		letter-spacing: 0.22rem;
		line-height: 1.1;
		text-align: center;

	}

}

.product_descr {
	width: 100%;

	color: #1b2124;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.5;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 3.4rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.8rem
		}
	}

	p {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.38;
	}
}

.product_form {
	width: 100%;
}

.product_form__controls {
	@extend %global_flex_block_row_wrap_flex-start;

	width: 100%;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 3.6rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 3rem;
		}
	}
}

.product_form__controls_col {
	@include bp($point_1 + 1, min) {
		&:first-child {
			width: calc(100% - 18rem);
			padding-right: 4rem;
		}
		&:last-child {
			width: 18rem;
			flex: none;
		}
	}

	@include bp($point_1) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}

		&:last-child {
			@include flex_block(row, wrap, flex-start, center, center);
		}
	}
}

.product_controls_title {
	width: 100%;

	color: $black_2;
	font-weight: 500;
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_4, min) {
		font-size: 1.4rem;
		letter-spacing: 0.08rem;
		line-height: 1.29;
	}

	@include bp($point_4 - 1) {
		font-size: 1.2rem;
		letter-spacing: 0.1rem;
		line-height: 1.17;
	}
}

.input_number {
	@extend %global_flex_block_row_wrap_flex-start;

	box-shadow: inset 0 0 0 1px $input_border_color;

	@include bp($point_1 + 1, min) {
		width: 100%;
		height: 4.5rem;
	}

	@include bp($point_1) {
		width: 47%;
		height: 4rem;
	}
}

.input_number_btn {
	position: relative;

	width: 5rem;
	height: 100%;

	cursor: pointer;

	&:before, &:after {
		@extend %transition_background;
	}

	&:before {
		position: absolute;
		top: 50%;
		left: 50%;

		content: '';

		width: 1.4rem;
		height: 1px;

		background-color: $red;

		transform: translate(-50%, -50%);
	}

	&.input_number_btn--increment_mod {
		&:after {
			position: absolute;
			top: 50%;
			left: 50%;

			content: '';

			width: 1px;
			height: 1.4rem;

			background-color: $red;

			transform: translate(-50%, -50%);
		}
	}

	&:hover, &:focus {
		&:before, &:after {
			background-color: $black_2;
		}
	}
}

.input_number_element {
	width: calc(100% - 10rem);
	height: 100%;

	color: $black_2;

	font-weight: 300;
	line-height: 1.4;
	text-align: center;

	border: 0;
	box-shadow: none;
	background: #fff0;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.5rem;
	}
}

.product_form__price {

	@include bp($point_1 + 1, min) {
		&.product_form__price--desktop_mod {
			width: 100%;
			&:not(:last-child) {
				margin-bottom: 3rem;
			}
		}

		&.product_form__price--mobile_mod {
			display: none;
		}
	}

	@include bp($point_1) {
		&:not(.product_form__price--mobile_mod) {
			display: none;
		}
		&.product_form__price--mobile_mod {
			@include flex_block(row, wrap, center, center, center);
			width: 53%;
			height: 4rem;
			padding: 0 1rem;
			text-align: right;
		}
	}
}

.product_form__price_title {
	width: 100%;

	color: $black_2;
	font-weight: 300;
	letter-spacing: 0.3rem;
	line-height: 1.27;
	text-transform: uppercase;

	@include bp($point_1 + 1, min) {
		font-size: 3rem;
	}

	@include bp($point_1) {
		font-size: 2rem;
	}
}

.product_form__btn {
	@extend %red_btn;
	@extend %btn_size_4;
}
