.form_select__field {
	position: relative;
}

.form_select__field_decor {
	@extend %transition_transform;

	z-index: 1;

	position: absolute;
	top: 50%;

	width: 1.8rem;
	height: 1.4rem;
	margin-top: -.7rem;

	background-image: url("../images/icons/other_icons/select_arrow.svg");
	background-repeat: no-repeat;
	background-size: contain;
	background-position: 50% 50%;

	pointer-events: none;

	.form_select__element:focus + & {
		transform: rotate(-180deg);
	}

	@include bp($point_1 + 1, min) {
		right: 2rem;
	}

	@include bp($point_1) {
		right: 1.4rem;
	}
}

.form_select__label {
	@extend %form_el_label;
}

.form_select__element {
	@extend %form_el_default;

	padding-right: 4rem;

	background: #fff0;

	-moz-appearance:none;
	-webkit-appearance:none;
	appearance:none;

	@include bp($point_1 + 1, min) {
		height: 4.5rem;
	}

	@include bp($point_1) {
		height: 4rem;
	}
}
