//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: var(--height-header);
	padding: 0 var(--gl-indent);

	font-size: 1rem;

	will-change: transform;

	@include z-index(header);

	&:before {
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;

		content: '';

		width: 100%;
		height: 100%;

		pointer-events: none;

		background-color: transparent;

		transition: background-color .3s ease;

		body.body--scroll_state:not(.body--open_menu_state) & {
			background-color: $white
		}

		body.body--open_menu_state & {
			background-color: $black_2;
		}
	}
}

.header__in {
	@extend %global_flex_block_row_wrap_flex-start_center;

	position: relative;

	width: 100%;
	max-width: $page_width;
	height: 100%;
	margin: 0 auto;
}

.header__decor {
	position: absolute;
	left: 0;
	bottom: 0;

	width: 100%;
	height: 1px;

	background-color: $white;

	body.body--header_mod:not(.body--open_menu_state) & {
		background-color: $black_2;
	}
}

.header__logo {
	position: relative;

	@include bp($point_2, min) {
		width: 17.4rem;
		height: 3rem;
	}

	@include bp($point_3) {
		width: 11rem;
		height: 2rem;
	}
}

.header__logo_in {
	@extend %transition_opacity;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	&:not(.header__logo_in--hidden_mod) {
		body.body--header_mod:not(.body--open_menu_state) &,
		body.body--scroll_state:not(.body--open_menu_state) & {
			opacity: 0;
		}
	}

	&.header__logo_in--hidden_mod {
		opacity: 0;

		body.body--header_mod:not(.body--open_menu_state) &,
		body.body--scroll_state:not(.body--open_menu_state) & {
			opacity: 1;
		}
	}
}

.header__logo_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

.header__side {
	@extend %global_flex_block_row_wrap_flex-start_center;

	height: 100%;
	margin-left: auto;
}

.header_contact {}

.header_contact__list {
	@extend %global_flex_block_row_wrap_flex-start_center;

	margin: 0 -2.6rem 0 0;

	list-style: none;
}

.header_contact__list_item {
	padding: 0 2.6rem 0 0;

	@include bp($point_3) {
		&:first-child {
			padding: 0 1.4rem 0 0;	
		}
	}
}

.header_contact__phone {
	display: inline-flex;
	justify-content: flex-start;
	align-items: center;
	align-content: center;

	text-decoration: none;
	color: $red;
	font-size: 1.4rem;
	font-weight: 400;
	letter-spacing: 0.1rem;
	line-height: .8;

	&:hover {
		text-decoration: none;
	}
}

.header_contact__phone_number {
	@include bp($point_3) {
		display: none;
	}
}

.header_contact__phone_icon {
	display: block;

	color: currentColor;

	@include bp($point_2, min) {
		width: 1.5rem;
		height: 1.8rem;
		margin-right: .6rem;

		.header_contact__button & {
			display: none;
		}
	}

	@include bp($point_3) {
		width: 1.4rem;
		height: 1.4rem;
		margin-right: .4rem;
	}
}

.header_contact__button {
	@extend %red_btn;
	@extend %btn_size_2;

	min-width: 7.6rem;
}

.header_menu {
	position: relative;

	height: 100%;

	&:before {
		@extend %transition_background;

		position: absolute;
		top: 50%;
		left: 0;

		content: '';

		width: 1px;

		background-color: rgba($white, .8);


		body.body--header_mod:not(.body--open_menu_state) &,
		body.body--scroll_state:not(.body--open_menu_state) & {
			background-color: $black_2;
		}

		@include bp($point_2, min) {
			height: 5.6rem;
			margin-top: math(-5.6rem, 2);
		}

		@include bp($point_3) {
			height: 4rem;
			margin-top: math(-4rem, 2);
		}
	}

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center, center);

		margin-left: 4rem;
		padding-left: 3.6rem;
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, center, center, center);

		margin-left: 2.2rem;
		padding-left: 1.8rem;
	}
}

.header_menu__title {
	@extend %transition_color;

	display: block;


	color: $white;
	text-transform: uppercase;

	body.body--header_mod:not(.body--open_menu_state) &,
	body.body--scroll_state:not(.body--open_menu_state) & {
		color: $black_2;
	}

	@include bp($point_2, min) {
		min-width: 5.2rem;
		margin-right: 2rem;

		font-size: 1.4rem;
		font-weight: 300;
		letter-spacing: 0.08rem;
		line-height: 1.14;
		text-align: left;
	}

	@include bp($point_3) {
		order: 2;
		min-width: 3.4rem;
		margin-top: .6rem;

		font-size: 0.9rem;
		font-weight: 400;
		letter-spacing: 0.051rem;
		line-height: .8;
		text-align: center;
	}

}

//------------------------------------------------------------layout###
//---------------------------------------------trigger
@mixin user_menu_butt_line {
	@extend %transition_background;

	position: absolute;
	left: 0;

	width: 100%;
	height: .2rem;

	background-color: $white;

	pointer-events: none;

	body.body--header_mod:not(.body--open_menu_state) &,
	body.body--scroll_state:not(.body--open_menu_state) & {
		background-color: $black_2;
	}
}

.header_menu__trigger {
	z-index: 210;
	position: relative;

	text-transform: uppercase;
	color: transparent;

	cursor: pointer;

	&:before {
		top: 0;

		content: '';

		transition: all .3s ease;

		@include user_menu_butt_line;
	}

	&:after {
		bottom: 0;

		content: '';

		transition: all .3s ease;

		@include user_menu_butt_line;
	}

	body.body--open_menu_state & {
		@include bp($point_2, min) {
			&:before {
				transform: (rotate(45deg) translate(.5rem, .6rem));
			}

			&:after {
				transform: (rotate(-45deg) translate(0.5rem, -.6rem));
			}
		}

		@include bp($point_3) {
			&:before {
				transform: rotate(45deg);
				transform-origin: 0.7rem 0.3rem;
			}

			&:after {
				transform: rotate(-45deg);
				transform-origin: .4rem -.2rem;
			}
		}
	}

	@include bp($point_2, min) {
		width: 2.8rem;
		height: 1.8rem;
	}

	@include bp($point_3) {
		width: 2.6rem;
		height: 1.4rem;

		order: 1;
	}
}

.header_menu__trigger_decor {
	top: 50%;
	margin-top: -.1rem;

	transition: all .3s ease;

	body.body--open_menu_state & {
		opacity: 0;
		transform: translate(100%, 0);
	}

	@include user_menu_butt_line;
}

