//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-footer: 44rem;

	@include bp($point_2, min) {
		--gl-indent: 3rem;
		--height-header: 10rem;
	}
	@include bp($point_3) {
		--gl-indent: 2.5rem;
		--height-header: 6.8rem;
	}
}

html {
	height: 100%;
	font: 400 10px/1.33 $font_1;

	@include bp($point_7 - 1) {
		font-size: 9px;
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	opacity: 0;

	&.loaded_mod {
		opacity: 1;
	}

	@supports (display: grid) {
		display: block;
	}

	&.body--popup_open_state,
	&.body--open_menu_state {
		overflow: hidden;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;
	padding: 0;

	flex: 1 0 auto;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}

}

sup {
	font-size: .5em;
	font-family: $font_3;
	vertical-align: super;
	line-height: 1;
}

.elements_list {
	padding: 3rem;
}

//------------------------------------------------------------base styles###
