.email_form {
	@extend %global_flex_block_column_nowrap_flex-start_center;
}

.email_form__logo_img_w {
	margin-bottom: 2.8rem;
	width: 8.8rem;
	height: 8.8rem;
}

.email_form__logo_picture {
	display: block;
	width: 100%;
	height: 100%;
}

.email_form__logo_img {
	@extend %contain_img;
}

.email_form__title {
	margin-bottom: 1rem;
	
	font-weight: 300;
	font-size: 2.4rem;
	line-height: 1.17;
	font-family: $font_1;
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 0.3rem;
	color: $black_2;
}

.email_form__cta_title {
	margin-bottom: 1.6rem;
	
	font-weight: 300;
	line-height: 1.11;
	font-family: $font_2;
	text-align: center;
	letter-spacing: -0.045rem;
	color: $black_2;

	@include bp($point_4, min) {
    font-size: 4.5rem;
  }
  
  @include bp($point_4 - 1) {
    font-size: 3.6rem;
  }
}

.email_form__cta_text {
	margin-bottom: 3.8rem;
	max-width: 54.6rem;
	
	font-weight: 400;
	font-size: 1.6rem;
	line-height: 1.5;
	font-family: $font_1;
	text-align: center;
	color: $black_2;
}

.email_form__fields_w {
	margin-bottom: 4.4rem;
	max-width: 61.2rem;
	
	overflow: hidden;
}

.email_form__fields {
	@extend %global_flex_block_row_wrap_flex-start;

	margin: 0 -2rem -2rem 0;
}

.email_form__field_w {
	padding: 0 2rem 2rem 0;

	&.email_form__field_w--width_1 {
		width: 50%;

		@include bp($point_4 - 1) {
			width: 100%;
		}
	}

	&.email_form__field_w--width_2 {
		width: 100%;
	}
}

.email_form__submit_button {
	@extend %red_btn;
	@extend %btn_size_6;
}