.contact_us--mod_2 {
	.section__in {
		position: relative;
	}

	@include bp($point_2, min) {
		padding-top: 4.4rem;
		padding-bottom: 4rem;

		.section__in {
			padding-top: 7rem;
		}
	}

	@include bp($point_3) {
		.section__in {
			padding-top: 4rem;
		}
	}
}

.contact_us__main_title {
	@extend %section_title;
	@extend %h4;

	margin-bottom: 4.6rem;
}

.contact_us__w {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	&.contact_us__w--mod_2 {
	}

	@include bp($point_3) {
		flex-direction: column;
	}
}

.contact_us__info {
	@include bp($point_2, min) {
		width: 35%;

		padding-top: 4rem;
		padding-right: 3rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.contact_us__title,
.contact_us__form_title {
	@extend %h4;
	@extend %title_offset;
	letter-spacing: 3px;
}

.contact_us__text {
	line-height: 1.5;

	a {
		color: $black_2;
		text-decoration: underline;
	}

	.contact_us__w--mod_2 & {
		max-width: 39rem;
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
		margin-bottom: 3.2rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
		margin-bottom: 1.8rem;
	}
}

.contact_us__contacts {
	width: 100%;

	@include bp($point_2, min) {
	}

	@include bp($point_3) {
		margin-bottom: 3.6rem;
	}

	&.contact_us__contacts--v2-mod {
		@include bp($point_2, min) {
			margin-bottom: 4.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 2rem;

			&:last-child {
				margin-bottom: 3.6rem;
			}
		}
	}
}

.contact_us__contacts_item {
	@include bp($point_2, min) {
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.3rem;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}

	.contact_us__contacts--v2-mod & {
		margin-bottom: 0.2rem;
	}
}

.contact_us__contacts_title {
	@extend %h5;

	letter-spacing: 3px;

	@include bp($point_2, min) {
		margin-bottom: 1.8rem;
	}

	@include bp($point_3) {
		margin-bottom: 0.4rem;
	}
}

.contact_us__contacts_link {
	color: $black_2;
	text-decoration: underline;
}

//-------------------- form styles

.contact_us__form_w {
	background-color: $white;

	@include bp($point_2, min) {
		width: 65%;
		padding: 4rem 5.8rem;
	}

	@include bp($point_3) {
		padding: 2.2rem 2.8rem 4rem;
		margin-bottom: 4rem;
		order: -1;
	}
}

.contact_us__form_title {
	margin-bottom: 2.6rem;
}

.contact_form__field_w {
	@extend %global_flex_block_row_wrap_flex-start_stretch;
	position: relative;

	padding-bottom: 2.4rem;

	@include bp($point_2, min) {
		margin: 0 -3rem -3rem 0;
	}

	@include bp($point_3) {
		// flex-direction: column;
		margin: 0 0 -1.6rem 0;
		// margin: 0 0 -1.6rem 0;
	}

	&.contact_form__field_w--offset-mod {
		margin: 0;

		@include bp($point_2, min) {
			padding-bottom: 4rem;
		}

		@include bp($point_3) {
			padding-bottom: 2.8rem;
		}
	}

	&.contact_form__field_w--offset-2-mod {
		margin: 0;
		padding-bottom: 2rem;
	}

	&.contact_form__field_w--v2-mod {
		display: block;

		@include bp($point_2, min) {
			width: 73%;
		}
	}

	&.contact_form__field_w--decor-mod {
		&::before {
			position: absolute;
			left: 0;

			width: 100%;
			height: 1px;

			background-color: $gray;

			content: "";

			@include bp($point_2, min) {
				bottom: 3rem;
			}

			@include bp($point_3) {
				bottom: 2.2rem;
			}
		}
	}
}

.form_input {
	@include bp($point_4, min) {
		width: 50%;
		padding: 0 3rem 3rem 0;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		padding: 0 0 1.6rem 0;
	}

	.contact_us__form_list--checkbox & {
		width: unset;
	}

	.email_form__field_w & {
		padding: 0;
		width: unset;
	}

	.form_input__element {
		::-webkit-contacts-auto-fill-button {
			visibility: hidden;
			position: absolute;
			right: 0;
		}
	}
}

.form_textarea {
	// width: 100%;

	// @include bp($point_4, min) {
	// 	padding: 0 3rem 3rem 0;
	// }

	// @include bp($point_4 - 1) {
	// 	padding: 0 0 1.6rem 0;
	// }
}

.contact_form__text {
	width: 100%;
	margin-bottom: 1.2rem;

	@include bp($point_2, min) {
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.form_checkbox {
	@include bp($point_2, min) {
		padding: 0 5.4rem 2rem 0;
	}

	@include bp($point_3) {
		padding: 0 0 2rem 0;
		width: 100%;
	}

	&:nth-child(3) {
		@include bp($point_2, min) {
			padding: 0 9.2rem 2rem 0;
		}
	}
}

.form_checkbox__label--size_mod {
	@include bp($point_2, min) {
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.contact_form__btn_w {
	margin-top: 1.8rem;
}

.contact_form__btn {
	@extend %red_btn;
	@extend %btn_size_1;

	letter-spacing: 0.88px;

	@include bp($point_3) {
		min-width: 100%;
	}
}
