.map__title {
	@extend %h4;

	letter-spacing: .3rem;
	
	@include bp($point_2, min) {
		margin-bottom: 12rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}

	&.desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&.mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.map__cols {
	@extend %global_flex_block_row_nowrap_flex-start;

	@include bp($point_2, min) {
		margin-right: -6rem;
	}

	@include bp($point_3) {
		flex-wrap: wrap;
	}
}

.map__col {
	@include bp($point_2, min) {
		padding-right: 6rem;
		
		&:first-child {
			width: 33%;
		}
		
		&:last-child {
			width: 67%;
		}
	}

	@include bp($point_3) {
		width: 100%;

		&:first-child {
			order: 2;
			margin-top: 3rem;
		}
	}
}

.map_locations {
	counter-reset: mapLocations;
}

.map_locations__item {
	margin-bottom: 2rem;
	
	font-weight: 300;
	
	border-bottom: 1px solid $black_2;
	cursor: pointer;
	
	@include bp($point_2, min) {
		padding-bottom: 3.2rem;
		font-size: 2rem;
	}

	@include bp($point_3) {
		padding-bottom: 1.4rem;
		font-size: 1.8rem;
	}

	&:before {
		counter-increment: mapLocations;
		content: counter(mapLocations);

		margin-right: 3rem;
		font-weight: 500;
	}
	

	&:hover, &:focus {
		color: $red;
	}
}

.map {
	width: 100%;
	background-color: #2b2b2b;
	
	@include bp($point_2, min) {
		height: 72rem;
	}

	@media (max-width: $point_3) and (min-width: $point_4) {
		height: 50rem;
	}

	@include bp($point_4 - 1) {
		height: 26.8rem;
	}
}

.map_popup {
	max-width: 25rem;

	h3 {
		margin-bottom: .6rem;

		font-size: 1.6rem;
	}

	p {
		margin-bottom: .6rem;

		font-size: 1.4rem;
	}

	a {
		font-size: 1.4rem;
		text-decoration: none;

		&:hover, &:focus {
			text-decoration: underline;
		}
	}
}