.formery_item {
	border: 1px solid;

	&.formery_item--mod_1 {
		border-color: #f0be41;
		color: #f0be41;
	}

	&.formery_item--mod_2 {
		border-color: #61ab9e;
		color: #61ab9e;
	}

	@include bp($point_2, min) {
		padding: 3.9rem 4.8rem 4.6rem;
	}

	@include bp($point_4, min) {
		width: 100%;
		height: 100%;

		@include flex_block(column, nowrap, flex-start);
	}

	@include bp($point_3) {
		padding: 1.9rem 2.4rem 2.3rem;
	}
}

.formery_item_logo {
	display: block;
	width: 11rem;
	height: 11rem;
	margin-left: auto;
	margin-right: auto;

	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}
}

.formery_item_logo__img {
	@extend %contain_img;
}

.formery_item__title {
	width: 100%;

	font-weight: 300;
	letter-spacing: 2.6px;
	text-align: center;
	text-transform: uppercase;
	color: inherit;

	&:not(:last-child) {
		margin-bottom: 2.2rem;
	}

	@include bp($point_2, min) {
		font-size: 2.6rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

.formery_item__text {
	text-align: center;
	color: $white;
	line-height: 1.5;

	margin-left: auto;
	margin-right: auto;

	.formery_item--mod_1 & {
		max-width: 46rem;
	}

	.formery_item--mod_2 & {
		max-width: 45rem;
	}

	p:not(:last-child) {
		margin-bottom: 0.7rem;
	}

	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_4, min) {
		flex: 1 1 auto;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

.formery_item__wrap {
	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 5rem;
		}
	}

	@include bp($point_4, min) {
		width: 100%;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.formery_item__btn {
	@extend %btn_size_7;
	@extend %white_btn;
}

.formery_item_img {
	position: relative;
	
	display: block;
	width: calc(100% + 2px);
	margin-left: -1px;
	padding-bottom: 56.666%;
}

.formery_item_img__in {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}
