//---------------------------------------------sections
.section {
	position: relative;
	width: 100%;
	
	@include bp($point_2, min) {
		padding: 8rem var(--gl-indent);

	}
	@include bp($point_3) {
		padding: 4rem var(--gl-indent);
	}
}

.section__in {
	max-width: $page_width;
	width: 100%;
	margin: 0 auto;
}
