.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;
	
	font-size: 1rem;
	
	background-color: $black_2;
	will-change: transform;
	
	@include bp($point_2, min) {
		padding: 6rem var(--gl-indent) 3.8rem;
	}

	@include bp($point_3) {
		padding: 4.2rem var(--gl-indent) 4rem;
	}
}

.footer__in {
	max-width: $page_width;
	margin: 0 auto;
	width: 100%;
}

.footer__logo {
	display: block;

	@include bp($point_2, min) {
		width: 17.4rem;
		height: 3rem;
		margin: 0 auto 4.4rem;
	}

	@include bp($point_3) {
		width: 13rem;
		height: 2.4rem;
		margin: 0 auto 3.2rem;
	}
}

.footer__logo__in {
	@extend %contain_img;
}

.footer__contacts {
	margin-bottom: 4.2rem;
	border-bottom: 1px solid rgba($gray, .2);
	
	@include bp($point_2, min) {
		padding-bottom: 5.4rem;
	}

	@include bp($point_3) {
		padding-bottom: 4rem;
	}
}

.footer__contacts__list {
	@extend %global_flex_block_row_wrap_center_center;

	@include bp($point_2, min) {
		margin: 0 -5.2rem -2rem 0;
	}
}

.footer__contacts__item {
	@include bp($point_2, min) {
		padding: 0 5.2rem 2rem 0;
	}
	
	@include bp($point_3) {
		width: 100%;
		margin-bottom: 1rem;
		text-align: center;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.footer__contacts__link {
	color: $white;
	
	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}

button.footer__contacts__link,
a.footer__contacts__link {
	cursor: pointer;
	transition: color .3s;

	&:hover, &:focus {
		text-decoration: none;
		color: rgba($white, .6);
	}
}

.footer__bottom {
	@extend %global_flex_block_row_wrap_flex-start_center;

	@include bp($point_2, min) {
		margin-bottom: 6.6rem;
	}

	@include bp($point_3) {
		flex-wrap: wrap;
		margin-bottom: 4.2rem;
	}
}

.footer__bottom__logo {
	display: block;
	width: 11.8rem;
	
	@include bp($point_2, min) {
		margin-right: 2rem;
		height: 7rem;
	}

	@include bp($point_3) {
		margin: 0 auto 4rem;
		height: 6.8rem;
		transform: translateX(.8rem);
	}
}

.footer__bottom__logo__in {
	@extend %contain_img;
}

.footer__bottom__socials {
	@include bp($point_2, min) {
		margin-left: auto;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.footer__bottom__socials__tag {
	margin-bottom: 1rem;
	color: $white;
	
	@include bp($point_2, min) {
		font-size: 1.6rem;
		text-align: right;
	}

	@include bp($point_3) {
		font-size: 	1.3rem;
		text-align: center;
	}
}

.footer__bottom__socials__list {

}

.footer__copy {
	@extend %global_flex_block_row_wrap_center_center;

	@include bp($point_2, min) {
		margin: 0 -3.4rem -1rem 0;
	}

	@include bp($point_3) {
		margin: 0 -2.4rem -.4rem 0;
	}
}

.footer__copy__item {
	position: relative;

	& + & {
		&::after {
			position: absolute;
			
			width: .4rem;
			height: .4rem;
			
			background-color: rgba($white, .3);
			border-radius: 50%;
			content: '';
			
			@include bp($point_2, min) {
				top: .4rem;
				left: -1.8rem;
			}

			@include bp($point_3) {
				top: .5rem;
				left: -1.4rem;
			}
		}
	}

	@include bp($point_2, min) {
		padding: 0 3.4rem 1rem 0;
	}

	@include bp($point_3) {
		padding: 0 2.4rem .4rem 0;

		&:first-child {
			width: 100%;
			text-align: center;
			&:after {
				display: none;
			}
		}

		&:nth-child(2) {
			&::after {
				display: none;
			}
		}
	}
}

.footer__copy__link {
	color: rgba($white, .3);
	font-size: 1.2rem;
}


button.footer__copy__link,
a.footer__copy__link {
	cursor: pointer;
	transition: color .3s;

	&:hover, &:focus {
		color: $white;
		text-decoration: none;
	}
}
