.benefits__card {
	border-top: 1px solid $black_2;
	
	.benefits__list_item:nth-child(1) &,
	.benefits__list_item:nth-child(2) &,
	.benefits__list_item:nth-child(3) & {
	
		@include bp($point_2, min) {
			padding-top: 0;	
			border: none;
		}
	}

	@include bp($point_2, min) {
		padding-top: 1.2rem;
	}

	@include bp($point_3) {
		padding-top: 3.6rem;

		.benefits__list_item:first-child & {
			padding-top: 0;	
			border: none;
		}
	}
}

.benefits__card_number {
	font-weight: 300;
	line-height: .9;

	@include bp($point_2, min) {
		font-size: 6rem;
		margin-bottom: 2rem;
	}

	@include bp($point_3) {
		font-size: 3.2rem;
		margin-bottom: .6rem;
	}
}

.benefits__card_title {
	font-weight: 300;
	text-transform: uppercase;
	letter-spacing: 3px;

	@include bp($point_2, min) {
		font-size: 2.4rem;
		margin-bottom: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		margin-bottom: .6rem;
	}
}

.benefits__card_descr {
	font-weight: 400;
	line-height: 1.5;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
	}
}