.plans {
}

.plans__in {
	overflow: hidden;
}

.plans__title {
	@extend %section_title;

	@include bp($point_2, min) {
		margin-bottom: 4rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}


.plans__list {
	@extend %global_flex_block_row_wrap_flex-start_stretch;
	// @extend %workspace_border_bottom;

	// position: relative;
	@include bp($point_4, min) {
		margin: 0 -6rem -2.6rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -6rem 0 0;
	}
}

.plans__list__item {
	position: relative;

	padding: 0 6rem 5rem 0;

	@include bp($point_2, min) {
		width: 33.333%;
		&:not(:nth-child(3n+3)) {
			&:before {
				opacity: 1;
			}
		}
	}

	@media (max-width: $point_3) and (min-width: $point_4) {
		width: 50%;
		&:nth-child(odd) {
			&:before {
				opacity: 1;
			}
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;
		border-bottom: 1px solid $black_2;
		padding-bottom: 2rem;
		margin-bottom: 4rem;

		&:last-child {
			margin-bottom: 0;
		}
	}


	&:not(:nth-last-child(1)):not(:nth-last-child(2)):not(:nth-last-child(3)),
	&:first-child,
	&:nth-child(2),
	&:nth-child(3) {
		@include bp($point_2, min) {
			@include workspace_border_bottom;
		}
	}
	&:not(:nth-last-child(1)):not(:nth-last-child(2)),
	&:first-child,
	&:nth-child(2) {
		@media (min-width: $point_4) and (max-width: $point_3) {
			@include workspace_border_bottom;
		}
	}

	&::before {
		position: absolute;
		right: 3rem;
		top: 0;

		height: calc(100% - 5.8rem);
		width: 1px;

		background-color: $black_2;
		content: '';
		opacity: 0;
	}

	&:not(:nth-child(3n+3)) {
		&::before {
			height: calc(100% - 5rem);
		}
	}
}

