.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	padding: 8.4rem var(--gl-indent);

	font-size: 1rem;

	background-color: rgba($black_2, .8);

	z-index: 1000;
	overflow-y: auto;

	opacity: 0;
	pointer-events: none;

	&.popup--active {
		opacity: 1;
		pointer-events: initial;
	}
}

.popup_in {
	position: relative;
	
	width: 105.2rem;
	max-width: 100%;
	margin: 0 auto;
	
	background-color: $gray;
	
	@include bp($point_2, min) {
		padding: 6rem 9.6rem;
	}

	@include bp($point_3) {
		padding: 4rem var(--gl-indent);
	}
}

.popup_title {
	@extend %section_title;
	@extend %title_offset;
}

.popup_form__list {
	margin-bottom: 3.2rem;
}

.popup_form__list_in {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_4, min) {
		margin: 0 -3rem -3rem 0;
	}
}

.popup_form__btn_wrap {
	margin-bottom: 3rem;
}

.popup_form__btn {
	@extend %red_btn;
	@extend %btn_size_1;

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.popup_form__text {
	@include bp($point_2, min) {
		font-size: 1.6rem;
		line-height: 1.5;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.3;
	}
}

.popup_close {
	position: absolute;

	right: 1.4rem;
	bottom: calc(100% + 1.6rem);

	display: block;
	padding-right: 2.4rem;

	font-size: 1.4rem;
	color: $white;

	cursor: pointer;
	transition: color .3s;

	&:hover, &:focus {
		color: $red;

		&:before, &:after {
			background-color: $white;
		}
	}

	&:before, &:after {
		content: '';
		position: absolute;
		top: 50%;
		right: 0;
		height: 2.4rem;
		width: 1px;

		margin-top: -1.4rem;

		background-color: $red;
		transition: background-color .3s;
	}

	&:before {
		transform: rotate(45deg);
		transform-origin: 50% 50%;
	}

	&:after {
		transform: rotate(-45deg);
		transform-origin: 50% 50%;
	}
}