@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	font-family: $font_1;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

//color extends
%black_btn {
	text-transform: uppercase;
	font-weight: 500;
	color: $black_2;
	letter-spacing: 0.08rem;

	border-color: currentColor;
	background-color: transparent;

	transition: border-color 0.2s ease, color 0.2s ease;

	@include st_btn();

	&:hover,
	&:focus {
		color: $red;
		border-color: $red;
	}
}

%red_btn {
	text-transform: uppercase;
	font-weight: 500;
	color: $red;

	border-color: currentColor;
	background-color: transparent;

	transition: border-color 0.2s ease, color 0.2s ease;

	@include st_btn();

	&:hover,
	&:focus {
		border-color: $black_2;
	}
}

%white_btn {
	text-transform: uppercase;
	font-weight: 400;
	color: $white;
	letter-spacing: 0.02rem;

	border-color: currentColor;
	background-color: transparent;

	transition: border-color 0.2s ease, color 0.2s ease;

	@include st_btn();

	&:hover,
	&:focus {
		color: $red;
		border-color: $red;
	}
}

//size extends
%btn_size_1 {
	border: 1px solid;

	@include bp($point_2, min) {
		min-width: 13rem;
		padding: 0 2.4rem;
		height: 4.4rem;
		line-height: 1.28;
		font-size: 1.4rem;
	}
	@include bp($point_3) {
		min-width: 15rem;
		padding: 0 1.6rem;
		height: 4rem;
		line-height: 3.8rem;
		font-size: 1.2rem;
	}
}

%btn_size_2 {
	border: 1px solid;

	@include bp($point_2, min) {
		height: 4rem;
		min-width: 11rem;
		padding: 0 1.2rem;

		line-height: 4rem;
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		height: 3.2rem;
		min-width: 10rem;
		padding: 0 1rem;

		line-height: 3.2rem;
		font-size: 1.2rem;
	}
}

%btn_size_3 {
	border: 1px solid;
	padding: 0 1.2rem;

	@include bp($point_2, min) {
		height: 5rem;
		min-width: 23rem;

		line-height: 5rem;
		font-size: 1.6rem;
	}
	@include bp($point_3) {
		height: 4rem;
		min-width: 18rem;

		line-height: 4rem;
		font-size: 1.4rem;
	}
}

%btn_size_4 {
	height: 4.5rem;
	padding: 0 1.2rem;

	line-height: 4.5rem;
	font-size: 1.4rem;

	border: 1px solid;

	@include bp($point_6 + 1, min) {
		min-width: 22rem;
	}

	@include bp($point_6) {
		width: 100%;
	}
}

%btn_size_5 {
	border: 1px solid;
	padding: 0 1.2rem;

	letter-spacing: 0.1em;

	@include bp($point_2, min) {
		height: 5rem;
		min-width: 23rem;

		line-height: 5rem;
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		height: 5.2rem;
		width: 100%;
		min-width: 18rem;

		line-height: 4rem;
		font-size: 1.4rem;
	}
}

%btn_size_6 {
	border: 1px solid;

	padding: 0 1.6rem;
	min-width: 14.4rem;
	height: 4.4rem;

	line-height: 4.2rem;
	font-size: 1.4rem;
}

%btn_size_7 {
	border: 1px solid;
	padding: 0 1.2rem;

	letter-spacing: 0.06em;

	height: 4.8rem;
	min-width: 14.8rem;

	line-height: 4.8rem;
	font-size: 1.4rem;
}

%btn_size_8 {
	border: 1px solid;
	padding: 1.25rem 2.5rem;

	min-width: 14.8rem;

	font-size: 1.4rem;
	font-weight: 500;
	line-height: 1.28;
	text-align: center;
	text-transform: uppercase;
	letter-spacing: 0.8px;

	transition: border-color 0.2s ease, color 0.2s ease;

	&:hover,
	&:focus {
		color: $red;
		border-color: $red;
	}
}
