.accordion_item {
	border-bottom: 1px solid $black_2;

	&:first-child {
		border-top: 1px solid $black_2;
	}
}

.accordion_title {
	@extend %global_flex_block_row_wrap_flex-start_center;

	position: relative;

	padding: 2rem 5rem 2rem 0;

	cursor: pointer;

	@include bp($point_2, min) {
		min-height: 10rem;
	}
	@include bp($point_3) {
		min-height: 6.6rem;
	}
}

.accordion_title_text {
	@extend %h5;
	letter-spacing: 3px;
}

.accordion_decor {
	position: absolute;
	top: 50%;
	right: .6rem;

	transform: translate(0, -50%);

	@include bp($point_2, min) {
		width: 2.6rem;
		height: 2.6rem;
	}
	@include bp($point_3) {
		width: 2rem;
		height: 2rem;
	}

	&:after,
	&:before {
		position: absolute;
		top: 0;
		left: 50%;

		width: 2px;
		height: 100%;

		background-color: $red;
		border-radius: 1px;

		content: '';
	}

	&:after {
		.accordion_item.active_mod & {
			opacity: 0;
		}
	}

	&:before {
		transform: rotate(90deg);
    transform-origin: 50% 50%;
	}
}

.accordion_content {
	@include bp($point_2, min) {
		padding-bottom: 4.6rem;
	}
	@include bp($point_3) {
		padding-bottom: 2.6rem;
	}
}

.accordion_text {
	@include bp($point_2, min) {
		line-height: 1.5;
	}
	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.4;
	}

	p {
		@include bp($point_2, min) {
			margin-bottom: 1.6rem;
		}
		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}