.social_list {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin: 0 -2rem -2rem 0;
	padding: 0;

	list-style: none;

	&.social_list--event_mod,
	&.social_list--article_mod {
		margin: 0 -1.6rem -1.6rem 0;
	}

	&.social_list--footer_mod {
		@include bp($point_3) {
			justify-content: center;
			margin: 0 -1.4rem -1.6rem 0;
		}
	}
}

.social_item {
	padding: 0 2rem 2rem 0;

	.social_list--event_mod &,
	.social_list--article_mod & {
		padding: 0 1.6rem 1.6rem 0;
	}

	.social_list--footer_mod & {
		@include bp($point_3) {
			padding: 0 1.4rem 1.6rem 0;
		}
	}
}

.social_link {
	@extend %global_flex_block_row_wrap_center_center;

	width: 4rem;
	height: 4rem;
	padding: 1rem;

	color: $white;

	border: 1px solid currentColor;
	border-radius: 50%;

	transition: color .3s ease, border-color .3s ease;

	&.social_link:hover,
	&.social_link:focus {
		color: $red;
	}

	.social_list--event_mod &,
	.social_list--article_mod & {
		color: $black_2;
	}
}
