.testimonial__title {
	@extend %section_title;

	@include bp($point_2, min) {
		margin-bottom: 4rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.testimonial {
	background-color: $white;
	
	@include bp($point_2, min) {
		padding: 5.8rem 6rem 8.4rem;
	}

	@include bp($point_3) {
		padding: 2rem 3rem 8.4rem;
	}
}

.testimonial__content {
	max-width: 69rem;
	margin: 0 auto;
}

.testimonial__logo {
	display: block;
	@include bp($point_2, min) {
		width: 11.6rem;
		height: 11.6rem;
		margin: 0 auto 5.6rem;
	}

	@include bp($point_3) {
		width: 7.2rem;
		height: 7.2rem;
		margin: 0 auto 3.4rem
	}
}

.testimonial__logo_img {
	@extend %contain_img;
}

.testimonial__text {
	position: relative;

	font-family: $font_2;
	font-weight: 300;
	text-align: center;
	
	@include bp($point_2, min) {
		margin: 0 0 6rem;

		font-size: 3.5rem;
		letter-spacing: -0.035rem;
		line-height: 1.14;
	}

	@include bp($point_3) {
		margin: 0 0 1.8rem;

		font-size: 2rem;
		letter-spacing: -0.018rem;
		line-height: 1.2;
	}
}

.testimonial__decor {
	position: absolute;

	background-color: $black_2;
	
	@include bp($point_2, min) {
		width: 5.2rem;
		height: 8rem;
	}

	@include bp($point_3) {
		width: 2rem;
		height: 3rem;
	}

	&.testimonial__decor--v1_mod {
		@include bp($point_2, min) {
			bottom: -3rem;
			left: -15rem;
			
			@include decor(1.4rem, left);
		}

		@include bp($point_3) {
			bottom: -10rem;
			left: -.6rem;
			@include decor(.6rem, left);
		}
	}

	&.testimonial__decor--v2_mod {
		@include bp($point_2, min) {
			top: -3rem;
			right: -15rem;
			
			@include decor(1.4rem, right);
		}

		@include bp($point_3) {
			top: -4rem;
			right: -1rem;
			@include decor(.6rem, right);
		}
	}
}

.testimonial__author {
	font-weight: 300;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 2.4rem;
		letter-spacing: 0.3rem;
	}

	@include bp($point_3) {
		font-size: 1.8rem;
		letter-spacing: .18rem;
	}
}