.bottom_block {
	@include bp($point_3) {
		padding-top: 0;
	}
}

.bottom_block__logo {
	margin: 0 auto;
	
	@include bp($point_2, min) {
		height: 11.6rem;
		width: 11.2rem;
	}

	@include bp($point_3) {
		width: 7rem;
		height: 7rem;
	}
}

.bottom_block__logo__img {
	@extend %contain_img;
}